<!--  -->
<template>
  <div>
    <el-form ref="form" :inline="true">
      <p>   {{searchForm.org}}  <a href="javaScript:void(0)" @click="openTreeDialog"></a> </p>
      <el-form-item>
        <el-input v-model.trim="searchForm.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-select v-model.trim="searchForm.duty" clearable placeholder="">
          <el-option v-for="item in dutyGet" :label="item.label" :value="item.value" :key="item.value">
          </el-option>
        </el-select>
      </el-form-item>
    <!--   start-->
      <el-form-item label="" tag="div">
        <el-date-picker type="datetimerange" @change="dateChange" v-model.trim="dateValue" placeholder="">
        </el-date-picker>
        <el-button type="primary" @click="query"></el-button>
      </el-form-item>
    <!--   end-->
    </el-form>
    <!--   start -->
    <tree-menu :dialogState="isTreeDialog" ref="postinfo" @on-close="closeDialog" @post-node="getNodes">
    </tree-menu>
    <!--   end -->
    <!--  start-->
    <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" v-loading="tableLoading" element-loading-text="" style="width: 100%">
      <el-table-column prop="code" label="">
      </el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="duty" label="">
      </el-table-column>
      <el-table-column prop="orgName" label="">
      </el-table-column>
      <el-table-column prop="statement" label="">
      </el-table-column>
      <el-table-column label="" width="200">
        <template scope="scope">{{ scope.row.createTime.time | dateFormat }}</template>
      </el-table-column>
      <el-table-column prop="comments" label="">
      </el-table-column>
    </el-table>
    <br />
    <!--  end-->
    <!--   start-->
    <el-row>
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
        </el-pagination>
      </el-col>
    </el-row>
    <!--   end-->
  </div>
</template>
<script>
import { formatDate } from '@/base/date'
import treeMenu from '@/components/selectSector'

export default {
  components: {
    treeMenu,
  },
  data() {
    return {
      // 
      dutyGet: '',
      // 
      searchForm: {
        name: '',
        duty: '',
        org: '',
        orgId: '',
        dateVal: '',
      },
      // 
      isTreeDialog: false,
      // 
      dateValue: '',
      // 
      tableData: [],
      tableLoading: false,
      // 
      currentPage: 1, // 
      pageSize: 5, // 
      totalrecord: 0, // 
    }
  },
  methods: {
    // 
    getDuty() {
      this.$axios.get('staff_getDutyList.action').then(response => {
        this.dutyGet = JSON.parse(response.data)
      })
    },
    // 
    openTreeDialog() { // 
      this.isTreeDialog = true;
      this.$refs.postinfo.TreeQuery(); // 
    },
    closeDialog() { // 
      this.isTreeDialog = false;
    },
    getNodes(nodes) {
      this.isTreeDialog = false;
      this.searchForm.org = nodes[0].name
      this.searchForm.orgId = nodes[0].value
    },
    // 
    dateChange(val) {
      this.searchForm.dateVal = val;
    },
    // 
    query() {
      this.searchForm.currentPage = this.currentPage;
      this.getTable()
    },
    // 
    CurrentPageChange(val) {
      this.searchForm.currentPage = val;
      this.getTable()
    },
    getTable() {
      this.tableLoading = true;
      this.$axios.post('operationLog_query.action','jsonData=' + JSON.stringify(this.searchForm)).then(response => {
        this.tableLoading = false;
        try {
          console.table(JSON.parse(response.data))
          let funFmt = JSON.parse(response.data)
          let funPage = funFmt.pageview;
          this.pageSize = +funPage.page_size; // 
          this.totalrecord = +funPage.totalrecord; // 
          this.tableData = funPage.records; // 
        } catch (e) {
          this.tableData = [];
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.tableLoading = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
  },
  filters: {
    dateFormat(time) {
      if (time == undefined) { return ""; }
      var date = new Date(time);
      return formatDate(date, 'yyyy-MM-dd hh:mm:ss');
    }
  },
  created() {
    this.getDuty() // 
  }
}

</script>
<style lang="less" scoped>


</style>



