<!--  -->
<template>
  <div>
    <el-form ref="smsLogForm" :inline="true">
      <p>   {{smsLogForm.dept}}  <a href="javaScript:void(0)" @click="openTreeDialog"></a> </p>
      <el-form-item>
        <el-select v-model.trim="smsLogForm.Channel" clearable placeholder="">
          <el-option v-for="item in ChannelGet" :label="item.label" :value="item.value" :key="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-select v-model.trim="smsLogForm.adviser" placeholder="">
          <el-option label="" value="name"></el-option>
          <el-option label="" value="number"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-input v-if="smsLogForm.adviser === 'name'" v-model.trim="smsLogForm.nameOrUser" placeholder=""></el-input>
        <el-input v-if="smsLogForm.adviser === 'number'" v-model.trim="smsLogForm.nameOrUser" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-input v-model.trim="smsLogForm.CustomerNumber" placeholder=""></el-input>
      </el-form-item>
      <!--   start-->
      <el-form-item label="" tag="div">
        <el-date-picker type="datetimerange" @change="dateChange" v-model.trim="dateValue" placeholder="">
        </el-date-picker>
        <el-button type="primary" @click="QuerySms"></el-button>
        <el-button type="primary" @click="export2Excel"></el-button>
      </el-form-item>
      <!--   end-->
    </el-form>
    <!--   start -->
    <tree-menu :dialogState="isTreeDialog" ref="postinfo" @on-close="closeDialog" @post-node="getNodes">
    </tree-menu>
    <!--   end -->
    <!--   start-->
    <el-table ref="multipleTable" v-loading="tableLoading" :data="tableData" border tooltip-effect="dark" style="width: 100%">
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="code" label="">
      </el-table-column>
      <el-table-column prop="channel_name" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tag type="primary">{{scope.row.prtms}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="otherms" label="">
        <template scope="scope">
          <el-tag type="warning">{{scope.row.otherms}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column  prop="call_time"  label="" width="180px">
      </el-table-column>
      <el-table-column prop="content" :show-overflow-tooltip="true" label="">
      </el-table-column>
    </el-table>
    <br>
    <!--   end-->
    <!--   start-->
    <el-row>
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
        </el-pagination>
      </el-col>
    </el-row>
    <!--   end-->
  </div>
</template>
<script>
import treeMenu from '@/components/selectSector'

export default {
  components: {
    treeMenu,
  },
  data() {
    return {
      ChannelGet: [],
      // 
      smsLogForm: {
        dept: '',
        deptId: '',
        Channel: '',
        adviser: 'name',
        nameOrUser: '',
        CustomerNumber: '',
        // 
        dateVal: '',
      },
      // 
      isTreeDialog: false,
      // 
      dateValue: '',
      // ()
      tableData: [],
      tableLoading: false,
      // --
      currentPage: 1, // 
      pageSize: 5, // 
      totalrecord: 0, // 
      // ()
    };
  },
  methods: {
    // 
    getChannel() {
      this.$axios.get('channel_channelList.action').then(response => {
        this.ChannelGet = JSON.parse(response.data)
      })
    },
    // *******  *********
    // 
    openTreeDialog() { // 
      this.isTreeDialog = true;
      this.$refs.postinfo.TreeQuery(); // 
    },
    closeDialog() { // 
      this.isTreeDialog = false;
    },
    getNodes(nodes) {
      this.isTreeDialog = false;
      this.smsLogForm.dept = nodes[0].name
      this.smsLogForm.deptId = nodes[0].value
    },
    /*************  **************/
    // 2
    dateChange(val) {
      this.smsLogForm.dateVal = val;
    },
    // 
    QuerySms() {
      this.smsLogForm.currentPage = this.currentPage; // 
      this.getSmsTable()
    },
    // 
    CurrentPageChange(val) {
      this.smsLogForm.currentPage = val; // 
      this.getSmsTable()
    },
    // 
    getSmsTable() {
      this.tableLoading = true;
      this.$axios.post('callRecords_queryCallInMessage.action','jsonData=' + JSON.stringify(this.smsLogForm)).then(response => {
        this.tableLoading = false;
        try {
          console.log(JSON.parse(response.data))
          let outgoingFmt = JSON.parse(response.data)
          let outgoingData = outgoingFmt.pageview
          this.tableData = outgoingData.records; // 
          this.pageSize = +outgoingData.page_size; // 
          this.totalrecord = +outgoingData.totalrecord; // 
        } catch (e) {
          this.tableData = [];
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.tableLoading = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // 
    export2Excel() {
      require.ensure([], () => {
        const { export_json_to_excel } = require('@/vendor/Export2Excel');
        const tHeader = ["", "", "", "", "", "", ""];
        const filterVal = ["name", "code", "channel_name", "prtms", "otherms", "call_time", "content"];
        const data = this.formatJson(filterVal, this.tableData);
        export_json_to_excel(tHeader, data, "");
      })
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
  },
  created() {
    this.getChannel()
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



