<template>
  <el-row>
    <el-col :offset="2" :span="17">
      <br>
      <!--   start-->
      <el-form ref="InboundAnalyForm" :inline="true">
        <el-form-item label="">
          <el-select v-model.trim="InboundAnalysisForm.channelName" clearable placeholder="">
            <el-option v-for="item in ChannelGet" :label="item.label" :value="item.value" :key="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="InboundAnalysisForm.dept" style="width:225px" :disabled="true" placeholder=""></el-input> <a href="javaScript:void(0)" @click="openTreeDialog"></a>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model.trim="InboundAnalysisForm.duration" placeholder="">
            <el-option label=" " value="0"></el-option>
            <el-option label=" >10" value="10"></el-option>
            <el-option label=" >30" value="30"></el-option>
            <el-option label=" >40" value="40"></el-option>
            <el-option label=" >1" value="60"></el-option>
            <el-option label=" >5" value="300"></el-option>
            <el-option label=" >15" value="900"></el-option>
            <el-option label=" >30" value="1800"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="InboundAnalysisForm.name" style="width:225px" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker v-model.trim="dateValue" type="datetimerange" @change="dateChange" placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model.trim="InboundAnalysisForm.button">
            <el-radio label="media"></el-radio>
            <el-radio label="dept"></el-radio>
            <el-radio label="rest"></el-radio>
          </el-radio-group>
          <el-checkbox v-model.trim="InboundAnalysisForm.checkBox"></el-checkbox>
        </el-form-item>
        <el-form-item label="">
          <el-button type="primary" @click="QueryAnalysis"></el-button>
          <el-button type="primary" :loading="exportLoading" @click="export2file"></el-button>
          <!-- <span> <a href="">0</a></span> -->
          <!-- <span> <a href="">0</a></span> -->
        </el-form-item>
      </el-form>
      <!--  end-->
      <!--   start -->
      <tree-menu :dialogState="isTreeDialog" ref="postinfo" @on-close="closeDialog" @post-node="getNodes">
      </tree-menu>
      <!--   end -->
      <!--   start -->
      <el-dialog title="" :visible.sync="export2fileDialog" size="tiny">
        <span></span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="export2fileDialog = false"> </el-button>
          <a :href="fileUrl" target="_blank" :download="fileUrl">
            <el-button type="primary" @click="export2fileDialog = false"> </el-button>
          </a>
        </span>
      </el-dialog>
      <!--   end -->
      <!--   end -->
      <!--  start-->
      <el-table ref="multipleTable" v-loading="tableLoading" :data="tableData" border tooltip-effect="dark" style="width: 100%">
        <template v-if="InboundAnalysisForm.button  =='rest' ">
          <el-table-column prop="name" label=""></el-table-column>
        </template>
        <el-table-column v-if="InboundAnalysisForm.button !=='dept' " prop="channelName" label="">
        </el-table-column>
        <el-table-column v-if="InboundAnalysisForm.button  =='dept' " prop="orgName" label="">
        </el-table-column>
        <template v-if="InboundAnalysisForm.button  =='rest' ">
          <el-table-column prop="code" label=""></el-table-column>
        </template>
        <el-table-column prop="connTotal" label=""> </el-table-column>
        <el-table-column prop="disconnTotal" label=""> </el-table-column>
        <el-table-column prop="callTotal" label=""> </el-table-column>
        <el-table-column label="(%)">
          <template scope="scope">
            <span>{{scope.row.connRate}} %</span>
          </template>
        </el-table-column>
      </el-table><br />
    </el-col>
    <!--  end-->
    <!-- 1  start-->
    <el-col :offset="6" :span="5">
      <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
      </el-pagination>
    </el-col>
    <!-- 1  end-->
  </el-row>

</template>
<script>
import treeMenu from '@/components/selectSector'

export default {
  components: {
    treeMenu,
  },
  data() {
    return {
      ChannelGet: [],
      // 
      InboundAnalysisForm: {
        channelName: '',
        dept: '',
        deptId: '',
        name: '',
        duration: '0',
        button: 'media',
        checkBox: '',
        // 
        dateVal: '',
      },
      // 
      isTreeDialog: false,
      // 
      dateValue: '',
      // 
      tableData: [],
      // 
      export2fileDialog: false,
      exportLoading: false,
      fileUrl: '',
      // 
      currentPage: 1,
      pageSize: 5, // 
      totalrecord: 0, // 
    };
  },
  methods: {
    dateChange(val) {
      // console.log(val)
    },
    getChannel() {
      this.$axios.get('channel_channelList.action').then(response => {
        this.ChannelGet = JSON.parse(response.data)
      })
    },
    // *******  *********
    // 
    openTreeDialog() { // 
      this.isTreeDialog = true;
      this.$refs.postinfo.TreeQuery(); // 
    },
    closeDialog() { // 
      this.isTreeDialog = false;
    },
    getNodes(nodes) {
      this.isTreeDialog = false;
      this.InboundAnalysisForm.dept = nodes[0].name
      this.InboundAnalysisForm.deptId = nodes[0].value
    },
    /*************  **************/
    // 
    dateChange(val) {
      this.InboundAnalysisForm.dateVal = val;
    },
    // 
    QueryAnalysis() {
      this.InboundAnalysisForm.currentPage = this.currentPage;
      this.getAnalysisTable()
    },
    // 
    CurrentPageChange(val) {
      this.InboundAnalysisForm.currentPage = val;
      this.getAnalysisTable()
    },
    // 
    getAnalysisTable() {
      this.tableLoading = true;
      console.log(JSON.stringify(this.InboundAnalysisForm))
      this.$axios.post('callInAnalyze_queryCallInList.action','jsonData=' + JSON.stringify(this.InboundAnalysisForm)).then(response => {
        this.tableLoading = false;
        try {
          console.log(response.data)
          let outgoingFmt = JSON.parse(response.data)
          let outgoingData = outgoingFmt.pageview
          this.tableData = outgoingData.records; // 
          this.pageSize = +outgoingData.page_size; // 
          this.totalrecord = +outgoingData.totalrecord; // 
        } catch (e) {
          this.tableData = [];
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // 
    export2file() {
      this.exportLoading = true;
      this.$axios.post('callInAnalyze_exportExcel.action','jsonData=' + JSON.stringify(this.InboundAnalysisForm)).then(response => {
        // console.log(response.data)
        var res = JSON.parse(response.data)
        if (res.result == 'success') {
          // window.open(res.filepath);
          this.fileUrl = res.filepath;
          this.export2fileDialog = true;
        } else {
          this.$message({ message: res.cause, type: 'error' });
        }
          this.exportLoading = false;
      }, response => {
        this.exportLoading = false;
        this.$message({ message: "" + response, type: 'error' });
      })
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
  },
  created() {
    this.getChannel()
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



