<template>
  <div>
    <el-button type="primary" @click="clickAdd"></el-button>
    <!--  start-->
    <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" v-loading="tableLoading" element-loading-text="" style="width: 100%">
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="code" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">{{ scope.row.dataPermission === 1 ?'':'' }}</template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">{{ scope.row.functionPermission === 1 ?'':'' }}</template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">{{ scope.row.status === 'V'?'':'' }}</template>
      </el-table-column>
      <el-table-column label="" width="200px">
        <template scope="scope">
          <el-button size="small" type="text" @click="clickEdit(scope.$index, scope.row)"></el-button>
          <el-button size="small" type="text" @click="clickDel(scope.$index, scope.row)"></el-button>
          <!-- <el-button size="small" type="text" @click="RestPass(scope.row)"></el-button> -->
          <el-button size="small" type="text" @click="isThaw(scope.$index, scope.row)">
            {{ scope.row.status ==='V'?'':'' }}</el-button>
        </template>
      </el-table-column>
    </el-table>
    <br />
    <!--   end-->
    <!--   start-->
    <el-row>
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
        </el-pagination>
      </el-col>
    </el-row>
    <!--   end-->
    <!--   start-->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="AccountForm" :rules="rules" ref="AccountForm" label-width="80px">
        <el-form-item label="" prop="name">
          <el-input v-model.trim="AccountForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model.trim="AccountForm.code" auto-complete="off"></el-input>
        </el-form-item>
<!--         <el-form-item label="" prop="company">
          <el-select v-model.trim="AccountForm.company" style="width:100%" placeholder="">
            <el-option v-for="item in CompanyGet" :label="item.descrShort" :value="item.setId" :key="item.setId"></el-option>
          </el-select> -->
        </el-form-item>
        <el-form-item label="" prop="org">
           <span v-text="AccountForm.org"></span>  <a href="javaScript:void(0)" @click="openTreeDialog"></a>
        </el-form-item>
        <el-form-item label="">
          <el-checkbox label="" v-model.trim="AccountForm.dataPermission" :true-label="1" :false-label="0"></el-checkbox>
          <!-- <el-checkbox label="" v-model.trim="AccountForm.functionPermission" :true-label="1" :false-label="0"></el-checkbox> -->
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogOK('AccountForm')"> </el-button>
      </div>
    </el-dialog>
    <!--   end-->
    <!--   start  -->
    <tree-menu :dialogState.sync="isTreeDialog" ref="postinfo" @on-close="closeDialog" @post-node="getNodes">
    </tree-menu>
    <!--   end -->
  </div>
</template>
<script>
import treeMenu from '@/components/selectSector'

export default {
  components: {
    treeMenu,
  },
  data() {

    return {
      // 
      CompanyGet: [],
      // 
      isTreeDialog: false,
      // 
      tableData: [],
      tableLoading: false,
      dialogFormVisible: false,
      isAddEdit: 1,
      // 
      AccountForm: {
        org: '',
        orgId: '',
        code: '',
        // pwd: '',
        // checkPass: '',
        name: '',
        // company: '',
        // functionPermission: 0,
        dataPermission: 0,
      },
      // --
      rules: {
        name: { required: true, message: '', trigger: 'blur' },
        code: { required: true, message: '', trigger: 'blur' },
        // company: { required: true, message: '', trigger: 'change' },
        org: { required: true, message: '', trigger: 'change' },
      },
      // 
      currentPage: 1, // 
      pageSize: 5, // 
      totalrecord: 0, // 
    };
  },
  methods: {
    // 
    getCompany() {
      this.$axios.get('role_showCollectionIdAddUser.action').then(response => {
        this.CompanyGet = JSON.parse(response.data)
      })
    },
    // *******  *********
    // 
    openTreeDialog() { // 
      this.isTreeDialog = true;
      this.$refs.postinfo.TreeQuery(); // 
    },
    closeDialog() { // 
      this.isTreeDialog = false;
    },
    getNodes(nodes) {
      this.isTreeDialog = false;
      this.AccountForm.org = nodes[0].name
      this.AccountForm.orgId = nodes[0].value
    },
    // ***** 
    // 
    getAccount() {
      this.tableLoading = true;
      this.$axios.get('sysUser_getUserList.action?currentPage=' + this.currentPage).then(response => {
        console.log(response.data)
        this.tableLoading = false;
        let parseData = JSON.parse(response.data)
        let inData = parseData.pageview
        this.tableData = inData.records; // 
        this.pageSize = +inData.page_size; // 
        this.totalrecord = +inData.totalrecord; // 
      }, response => {
        this.$message({ message: "" + response, type: 'error' })
      })
    },
    // 
    CurrentPageChange(val) {
      this.tableLoading = true;
      this.currentPage = val;
      this.getAccount()
    },
    // 
    clickAdd() {
      this.isAddEdit = 1;
      this.dialogFormVisible = true;
      this.AccountForm = {
        org: '',
        orgId: '',
        code: '',
        name: '',
        // company: '',
        // functionPermission: 0,
        dataPermission: 0,
      };
    },
    // 
    clickEdit(index, row) {
      this.isAddEdit = 0;
      this.dialogFormVisible = true;
      this.AccountForm.id = row.userId;
      this.AccountForm.code = row.code;
      this.AccountForm.name = row.name;
      // this.AccountForm.company = row.setId;
      this.AccountForm.org = row.orgName;
      this.AccountForm.orgId = row.orgId;
      // this.AccountForm.functionPermission = row.functionPermission;
      this.AccountForm.dataPermission = row.dataPermission;
    },
    // 
    dialogOK(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let url;
          this.isAddEdit === 1 ?
            url = 'sysUser_addUser.action' :
            url = 'sysUser_update.action';
          console.log(url)
          console.log(JSON.stringify(this.AccountForm))
          this.$axios.post(url ,'jsonData=' + JSON.stringify(this.AccountForm)).then(response => {
            this.$message(JSON.parse(response.data))
            this.dialogFormVisible = false;
            this.getAccount();
            // 
            this.AccountForm = {
              org: '',
              orgId: '',
              code: '',
              name: '',
              // company: '',
              // functionPermission: 0,
              dataPermission: 0,
            };
          }, response => {
            this.$message({ message: "" + response, type: 'error' })
          })
        } else {
          return false;
        }
      });
    },
    // 
    clickDel(index, row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        this.$axios.get("sysUser_delete.action?id=" + row.userId).then(response => {
          this.$message({ message: "", type: 'success' });
          this.getAccount();
        }, response => {
          this.$message({ message: "" + response, type: 'error' })
        })

      }).catch(() => {
        this.$message({ message: '', type: 'info', });
      });
    },
    // -
    isThaw(index, row) {
      this.$axios.post("sysUser_blocked.action","jsonData=" + JSON.stringify({ "id": row.userId, "status": row.status })).then(response => {
        this.$message({ message: "", type: 'success' })
        this.getAccount();
      }, response => {
        this.$message({ message: "" + response, type: 'error' })
      })
    },
    // 
    RestPass(row) {
      console.log(row.userId)
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        this.$axios.get("sysUser_updatePassword.action?id=" + row.userId).then(response => {
          this.$alert('' + response.data, '', {
            confirmButtonText: '',
            callback: action => {
              this.$message({ type: 'info', message: "" });
            }
          });
        }, response => {
          this.$message({ message: "" + response, type: 'error' })
        })

      }).catch(() => {
        this.$message({ message: '', type: 'info', });
      });
    },
  },
  created() {
    // this.getCompany();
    this.getAccount();
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



