<template>
  <div>
    <el-dialog title="" :visible.sync="dialogState" @close="closeDialog" :modal="false">
      <el-tree :data="TreeData" :props="defaultProps" :load="loadNode" @node-click="TreeClick" ref="tree" lazy="" node-key="value" highlight-current show-checkbox :check-strictly="true" empty-text="">
      </el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogState = false"> </el-button>
        <el-button type="primary" @click="treeOk"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  props: {
    dialogState: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
      // 
      TreeData: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      },
    }
  },
  methods: {
    TreeQuery() {
      this.$axios.get('org_orgTreeLoad.action').then(response => {
        this.TreeData = JSON.parse(response.data)
        return false; // 
      }, response => {
        this.$message({ message: "" + response, type: 'error', })
      })
    },
    // 
    TreeClick(data) {
      this.$emit("on-tree-key", data.value, data.setId)
    },
    // &
    loadNode(node, resolve) {
      if (node.level > 0) {
        this.$once("on-tree-key", (id, setId) => {
          let ids = { id: id, setId: setId }
          this.$axios.post('org_childNodesLoad.action','jsonData=' + JSON.stringify(ids)).then(response => {
            return resolve(JSON.parse(response.data));
          }, response => {})
        })
      }
    },
    closeDialog() {
      this.$refs.tree.setCheckedNodes([])   // 
      this.$emit('on-close') // on-close 
    },
    treeOk() {
      console.log('keys', this.$refs.tree.getCheckedKeys())
      console.log('', JSON.stringify(this.$refs.tree.getCheckedNodes()))

      this.$emit('post-node', this.$refs.tree.getCheckedNodes()) // post-node 
    }
  },
  created() {

  },
}

</script>
<style lang="less" scoped>


</style>



