<!--  -->
<template>
  <div>
    <br />
    <!-- form  start -->
    <el-form ref="Nstateform" :inline="true">
      <el-form-item>
        <el-select v-model.trim="numberForm.state" placeholder="">
          <el-option label="" value="all"></el-option>
          <el-option label="" value="Y"></el-option>
          <el-option label="" value="I"></el-option>
        </el-select>
        <el-select v-model.trim="numberForm.channel" clearable placeholder="">
          <el-option v-for="item in ChannelGet" :label="item.label" :value="item.value" :key="item.value"></el-option>
        </el-select>
        <el-select v-model.trim="numberForm.nameOrNum" placeholder="">
          <el-option label="" value="name"></el-option>
          <el-option label="" value="number"></el-option>
        </el-select>
        <el-input v-model.trim="numberForm.UnameOrNum" style="width:190px" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="queryNumber"></el-button>
        <el-button type="primary" :loading="exportLoading" @click="export2Excel"></el-button>
        <!-- <el-button type="primary" @click="delNumber"></el-button> -->
      </el-form-item>
      <!-- <el-form-item> -->
      <!-- <el-button type="primary" @click="NewAddNumber"></el-button> -->
      <!-- <el-button type="primary" @click="openUpFileDialog(1)"></el-button> -->
      <!-- <el-button type="primary" @click="openUpFileDialog(2)"></el-button> -->
      <!-- </el-form-item> -->
    </el-form>
    <!-- form  end -->
    <!--   start -->
    <el-dialog title="" :visible.sync="export2fileDialog" size="tiny">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="export2fileDialog = false"> </el-button>
        <a :href="fileUrl" target="_blank" :download="fileUrl">
          <el-button type="primary" @click="export2fileDialog = false"> </el-button>
        </a>
      </span>
    </el-dialog>
    <!--   end -->
    <!--  &&   start-->
    <el-dialog title="" :visible.sync="dialogUpFile">
      <el-form :model="uploadFile">
        <el-form-item label="">
          <el-upload ref="uploadFile" action="/5i5j/number_importExcel.action" accept="text/csv" name="numFile" :auto-upload="false">
            <el-button type="primary" icon="upload"></el-button>
            <div slot="tip" class="el-upload__tip"></div>
          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <a href="/5i5j/assign-media.xls" v-if="isXls === 1">
            <el-button type="info" icon="document"></el-button>
          </a>
          <a href="/5i5j/assign-callout.xls" v-if="isXls === 2">
            <el-button type="info" icon="document"></el-button>
          </a>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogUpFile = false"> </el-button>
        <el-button type="primary" @click="pushAllot"> </el-button>
      </div>
    </el-dialog>
    <!--  &&   end-->
    <el-row>
      <!--  start-->
      <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" v-loading="tableLoading" element-loading-text="" style="width: 100%" @selection-change="tableSelectionChange">
   <!--      <el-table-column type="selection" width="50">
        </el-table-column> -->
        <el-table-column prop="acms" label="">
        </el-table-column>
        <el-table-column prop="city" label="">
        </el-table-column>
        <el-table-column prop="channel_name" label="">
        </el-table-column>
        <el-table-column prop="code" label="">
        </el-table-column>
        <el-table-column prop="name" label="">
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">{{ scope.row.state==='U'?'':'' }}</template>
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-button size="small" type="text" @click="seeNumber(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="text" @click="unBind(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <br>
      <!--  end-->
      <!--   start-->
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
        </el-pagination>
      </el-col>
      <!--   end-->
    </el-row>
  </div>
</template>
<script>
import bus from '@/bus' //

export default {
  data() {
    return {
      ChannelGet: [],
      //  select
      numberForm: {
        state: 'all',
        channel: '',
        nameOrNum: 'name',
        UnameOrNum: '',
      },
      // loding
      exportLoading: false,
      // 
      export2fileDialog: false,
      // 
      fileUrl:'',
      // 
      tableData: [],
      tableLoading: false,
      // 
      isXls: 1,
      // 
      dialogUpFile: false,
      uploadFile: {},
      // 
      // 
      pageSize: 5, // 
      totalrecord: 0, // 
      currentPage: 1, // 
      multipleSelection: [], // 
      // 
      InfoTable: [],
      page: 1
    }
  },
  methods: {
    // 
    getChannel() {
      this.$axios.get('channel_channelList.action').then(response => {
        this.ChannelGet = JSON.parse(response.data)
      })
    },
    // *****
    // 
    queryNumber() {
      this.numberForm.currentPage = this.currentPage;
      this.getNumberTable()
    },
    // 
    CurrentPageChange(val) {
      this.numberForm.currentPage = val;
      this.getNumberTable()
    },
    // 
    tableSelectionChange(val) {
      this.multipleSelection = val.map((r) => { return r.acms });
      console.log(this.multipleSelection)
    },
    // 
    getNumberTable() {
      this.tableLoading = true;
      this.$axios.post('number_queryNumberList.action','jsonData=' + JSON.stringify(this.numberForm)).then(response => {
        this.tableLoading = false;
        try {
          let numberData = JSON.parse(response.data)
          this.pageSize = +numberData.pageview.page_size; // 
          this.totalrecord = +numberData.pageview.totalrecord; // 
          this.tableData = numberData.pageview.records;
          console.log(numberData.pageview.records)
        } catch (e) {
          this.tableData = [];
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.tableLoading = false;
        this.$message({ type: 'error', message: "" + response, });
      })
    },
    // 
    NewAddNumber() {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /^[1-9]\d*$/,
        inputErrorMessage: ''
      }).then(({ value }) => {
        this.$axios.get('number_add.action?acms=' + value).then(response => {
          this.$message({ type: 'success', message: '' });
          this.queryNumber();
        }, response => {
          this.$message({ type: 'error', message: "" + response });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // 
    delNumber() {
      if (this.multipleSelection.length <= 0) {
        this.$message({ type: 'info', message: '' });
        return;
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.get("number_delete.action?acms=" + JSON.stringify(this.multipleSelection)).then(response => {
          this.$message({ type: 'success', message: '' });
          this.queryNumber();
        }, response => {
          this.$message({ type: 'error', message: "" + response });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // 
    // 
    export2Excel() {
      this.exportLoading = true;
      this.$axios.post('number_exportExcel.action','jsonData=' + JSON.stringify(this.numberForm)).then(response => {
        console.log(response.data)
        var res = JSON.parse(response.data)
        if (res.result == 'success') {
          // window.open(res.filepath);
          this.fileUrl = res.filepath;
          this.export2fileDialog = true;
        } else {
          this.$message({ message: res.cause, type: 'error' });
        }
        this.exportLoading = false;
      }, response => {
        this.exportLoading = false;
        this.$message({ message: "" + response, type: 'error' });
      })
    },
    //   &&  
    openUpFileDialog(isXls) {
      this.dialogUpFile = true;
      this.isXls = isXls; // 
    },
    //  && 
    pushAllot() {
      console.log("uploadAllot")
      this.$refs.uploadFile.submit();
      this.dialogUpFile = false;
      this.$message({ message: "", type: 'success', });
      this.queryNumber();
    },
    // 
    seeNumber(i, row) {
      let data = {
        "acms": row.acms,
        'currentPage': this.page
      }
      console.log(data)
      this.$axios.post('number_queryStaffByAcms.action','jsonData=' + JSON.stringify(data)).then(response => {
        let searchData = JSON.parse(response.data)
        if (searchData.totalrecord > 0) {
          this.$emit('on-search-table-info', response.data, "number")
        } else {
          this.$message({ type: 'info', message: "" });
        }
      }, response => {
        this.$message({ type: 'error', message: "" + response });
      })
    },
    // 
    unBind(i, row){
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.get('staff_singleUnSub.action?jsonData='+ JSON.stringify({"code":row.code,"acms":row.acms}) ).then(response => {
          this.$message({ message: "", type: 'success' });
          this.queryNumber();
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    }
  },
  created() {
    this.getChannel()
    bus.$on("on-number-page-change", (page) => {
      this.seeNumber()
      console.log("on-page", page)
      this.page = page || 1;
    })
  }
}

</script>
<style lang="less" scoped>
.el-dropdown-menu__item>p {
  margin: 0px;
}

</style>



