<!--  -->
<template>
  <div>
    <el-row>
      <el-col :span="4">
        <el-input v-model.trim="searchForm.roleName" placeholder=""></el-input>
      </el-col>
      <el-col :offset="1" :span="4">
        <el-select v-model.trim="searchForm.status" clearable placeholder="">
          <el-option label="" value="V"></el-option>
          <el-option label="" value="I"></el-option>
        </el-select>
      </el-col>
      <el-col :offset="1" :span="2">
        <el-button type="primary" :plain="true" icon="search" @click="query"></el-button>
      </el-col>
      <el-col :span="2">
        <el-button type="primary" icon="plus" @click="add"></el-button>
      </el-col>
    </el-row>
    <br />
    <!-- &&  start-->
    <el-dialog title="" :visible.sync="isDialog">
      <el-form :model="FunForm" :rules="rules" ref="FunForm" label-width="80px">
        <el-form-item label="" prop="roleName">
          <el-input v-model.trim="FunForm.roleName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model.trim="FunForm.status" clearable style="width:100%" placeholder="">
            <el-option label="" value="V"></el-option>
            <el-option label="" value="I"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="FunForm.comment" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isDialog = false"> </el-button>
        <el-button type="primary" @click="enter"> </el-button>
      </div>
    </el-dialog>
    <!-- &&  end-->
    <!--  start-->
    <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" v-loading="tableLoading" element-loading-text="" style="width: 100%">
      <el-table-column type="index" label="ID"  width="80">
      </el-table-column>
      <el-table-column prop="roleName" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">{{ scope.row.status ==='V'?'':'' }}</template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">{{ scope.row.statusDate.time | dateFormat }}</template>
      </el-table-column>
      <el-table-column prop="comment" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button size="small" type="danger" @click="delRow(scope.$index, scope.row)"></el-button>
          <el-button size="small" type="warning" @click="editRow(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <br />
    <!--  end-->
    <!--   start-->
    <el-row>
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
        </el-pagination>
      </el-col>
    </el-row>
    <!--   end-->
  </div>
</template>
<script>
import { formatDate } from '@/base/date'

export default {
  data() {
    return {
      // 
      CompanyGet: [],
      // &&
      FunForm: {
        roleName: '',
        status: '',
        comment: '',
      },
      // --
      rules: {
        roleName: { required: true, message: '', trigger: 'blur' },
        status: { required: true, message: '', trigger: 'blur' },
      },
      // 
      isDialog: false,
      isAddEdit: 1,
      // 
      searchForm: {
        roleName: '',
        status: '',
      },
      // 
      tableData: [],
      tableLoading: false,
      // 
      currentPage: 1, // 
      pageSize: 5, // 
      totalrecord: 0, // 
    }
  },
  methods: {
    // 
    add() {
      this.isDialog = true;
      this.isAddEdit = 1;
    },
    // 
    editRow(i, row) {
      this.isDialog = true;
      this.isAddEdit = 0;
      this.FunForm.roleId = row.roleId;
      this.FunForm.roleName = row.roleName;
      this.FunForm.status = row.status;
      this.FunForm.comment = row.comment;
    },
    // 
    enter() {
      this.$refs.FunForm.validate((valid) => {
        if (valid) {
          this.isDialog = false;
          this.tableLoading = true;
          let url;
          this.isAddEdit === 1 ?
            url = 'role_add.action?jsonData=' :
            url = 'role_update.action?jsonData='
          this.$axios.get(url + JSON.stringify(this.FunForm)).then(response => {
            this.tableLoading = false;
            this.isAddEdit === 1 ?
              this.$message({ message: "", type: 'success', }) :
              this.$message({ message: "", type: 'success', });
            this.query();
            this.FunForm = {
              roleName: '',
              status: '',
              comment: '',
            };
          }, response => {
            this.tableLoading = false;
            this.isAddEdit === 1 ?
              this.$message({ message: "" + response, type: 'error', }) :
              this.$message({ message: "" + response, type: 'error', });
            return false;
          })
        } else {
          return false;
        }
      });
    },
    // 
    query() {
      this.searchForm.currentPage = this.currentPage;
      this.getTable()
    },
    // 
    CurrentPageChange(val) {
      this.searchForm.currentPage = val;
      this.getTable()
    },
    getTable() {
      this.tableLoading = true;
      this.$axios.post('role_queryList.action','jsonData=' + JSON.stringify(this.searchForm)).then(response => {
        this.tableLoading = false;
        console.log(response.data)
        try {
          let funFmt = JSON.parse(response.data)
          let funPage = funFmt.pageview;
          this.pageSize = +funPage.page_size; // 
          this.totalrecord = +funPage.totalrecord; // 
          this.tableData = funPage.records; // 
        } catch (e) {
          this.tableData = [];
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.tableLoading = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // 
    delRow(i, row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        this.$axios.get('role_delete.action?roleId=' + row.roleId).then(response => {
          this.$message({ message: "", type: 'success', });
          this.query();
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })

      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },

  },
  filters: {
    dateFormat(time) {
      if (time == undefined) { return ""; }
      var date = new Date(time);
      return formatDate(date, 'yyyy-MM-dd hh:mm:ss');
    }
  },
  created() {
  }
}

</script>
<style lang="less" scoped>


</style>



