<template>
  <div>
    <el-button type="primary" @click="clickCL"></el-button>
    <el-button type="primary" @click="updataCL"></el-button>
    <!--  start-->
    <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" v-loading="tableLoading" element-loading-text="" style="width: 100%">
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <a :href="scope.row.url" target="_blank" :download="scope.row.url"></a>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">{{ scope.row.state==='V' ?'':'' }}</template>
      </el-table-column>
       <el-table-column prop="descrShort" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button size="small" type="danger" @click="DelCL(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table><br />
    <!--  end-->
    <!--   start-->
    <el-row>
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
        </el-pagination>
      </el-col>
    </el-row>
    <!--   end-->
    <!--  start-->
    <el-dialog title="" :visible.sync="dialogCL">
      <el-form :model="pushcl" label-width="80px">
        <el-form-item label="">
          <el-input v-model.trim="CLform.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model.trim="CLform.city" placeholder="" style="width: 100%">
            <el-option v-for="item in CityGet" :label="item.descrShort" :value="item.setId" :key="item.setId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-upload ref="uploadCL" action="/5i5j/ringtone_uploadRingtone.action" accept="audio/*" name="bgm" :data="{'form': JSON.stringify(CLform)}" :on-success="flieSuccess" :on-error="fileError" :file-list="fileList" :auto-upload="false">
            <el-button type="primary" icon="upload"></el-button>
            <div slot="tip" class="el-upload__tip">mp3wav6M</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogCL = false"> </el-button>
        <el-button type="primary" @click="pushCL"> </el-button>
      </div>
    </el-dialog>
    <!--  end-->
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      CityGet:[],
      // 
      tableData: [],
      tableLoading: false,
      // 
      CLform:{
        name:'',
        city:''
      },
      fileList: [],
      pushcl: {},
      dialogCL: false,
      // --
      currentPage: 1, // 
      pageSize: 8, // 
      totalrecord: 0, // 

    };
  },
  methods: {
    // ***** 
    clickCL() {
      this.dialogCL = true
      this.CLform = {
        name:'',
        city:''
      },
      this.fileList = [];
      this.pushcl = {};
      this.getCity()
    },
    // 
    getCity() {
      this.$axios.get('role_showCollectionIdBySetId.action').then(response => {
        this.CityGet = JSON.parse(response.data)
      })
    },
    // 
    pushCL() {
      console.log("pushCL")
      this.$refs.uploadCL.submit();
      this.dialogCL = false;
    },
    // 
    flieSuccess(response, file, fileList) {
      console.log("flieSuccess:", response)
      var res = JSON.parse(response)
      if (res.result == 'success') {
      this.$message({ message: "", type: 'success' });
        this.updataCL();
      } else {
        this.$message({ message: res.cause, type: 'error' });
      }
    },
    // 
    fileError(err, file, fileList) {
      this.$message({ message: "", type: 'error' });
    },
    // 
    updataCL() {
      this.tableLoading = true;
      this.$axios.get('ringtone_getfindRingtone.action?currentPage=' + this.currentPage).then(response => {
        this.tableLoading = false;
        console.log(response.data)
        try {
           let parseData = JSON.parse(response.data)
            let inData = parseData.pageview
            this.tableData = inData.records; // 
            this.pageSize = +inData.page_size; // 
            this.totalrecord = +inData.totalrecord; // 
        } catch (e) {
          this.tableData = [];
          this.$message({ message: "", type: 'error' });
        }
      }, response => {
        this.$message({ message: "" + response, type: 'error' })
      })
    },
    // 
    DelCL(index,row) {
      this.$axios.get('ringtone_deleteRingtone.action?id=' +row.id).then(response => {
        this.$message({ message: "", type: 'success'});
        this.updataCL();
      }, response => {
        this.$message({ message: "" + response, type: 'error' })
      })
    },
        // 
   CurrentPageChange(val) {
      this.tableLoading = true;
      this.currentPage = val;
      this.updataCL()
    },

  },
  created() {}
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



