<!--  -->
<template>
  <div>
    <el-form ref="OutgoForm">
      <p>   {{formList.dept}}  <a href="javaScript:void(0)" @click="openTreeDialog"></a> </p>
      <el-form-item>
        <el-select v-model.trim="formList.adviser" placeholder="">
          <el-option label="" value="name"></el-option>
          <el-option label="" value="number"></el-option>
        </el-select>
        <el-input v-model.trim="formList.nameOrUser" style="width:200px" placeholder=""></el-input>
        <el-select v-model.trim="formList.customer" placeholder="">
          <el-option label="" value="name"></el-option>
          <el-option label="" value="number"></el-option>
        </el-select>
        <el-input v-model.trim="formList.CustomerNumber" style="width:200px" placeholder=""></el-input>
      </el-form-item>
    </el-form>
    <!--   start -->
    <tree-menu :dialogState="isTreeDialog" ref="postinfo" @on-close="closeDialog" @post-node="getNodes">
    </tree-menu>
    <!--   end -->
    <!--   start-->
    <el-form ref="Timeform" :inline="true">
      <el-form-item label="" tag="div">
        <el-date-picker type="datetimerange" @change="dateChange" v-model.trim="dateValue" placeholder="">
        </el-date-picker>
          <el-checkbox v-model.trim="formList.isDiff"></el-checkbox>
        <el-button type="primary" @click="query"></el-button>
        <el-button type="primary" @click="emitExport2Excel"></el-button>
      </el-form-item>
    </el-form>
    <!--   end-->
  </div>
</template>
<script>
import treeMenu from '@/components/selectSector'

export default {
  components: {
    treeMenu,
  },
  data() {
    return {
      // 
      formList: {
        dept: '',
        deptId: '',
        adviser: 'name',
        nameOrUser: '',
        customer: 'name',
        CustomerNumber: '',
        isDiff: false,
        dateVal: '',
      },
      // 
      isTreeDialog: false,
      // 
      dateValue: '',
    };
  },
  methods: {
    // *******  *********
    // 
    openTreeDialog() { // 
      this.isTreeDialog = true;
      this.$refs.postinfo.TreeQuery(); // 
    },
    closeDialog() { // 
      this.isTreeDialog = false;
    },
    getNodes(nodes) {
      this.isTreeDialog = false;
      this.formList.dept = nodes[0].name
      this.formList.deptId = nodes[0].value
    },
    // 
    dateChange(val) {
      this.formList.dateVal = val;
    },
    // 
    query() {
      this.formList.currentPage = 1;
      this.emitTable()
    },
    // 
    emitTable() {
      this.$emit("on-change-query", this.formList)
    },
    // 
    emitExport2Excel() {
      this.$emit("on-export-excel")
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



