<!--  -->
<template>
  <div>
    <!--  start-->
    <head-form @on-change-query="getTableData" @on-export-excel="export2Excel" ref="search"></head-form>
    <!--  end-->
    <!--   start-->
    <el-table ref="multipleTable" v-loading="tableLoading" :data="respData" border tooltip-effect="dark" style="width: 100%">
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="" label="">
      </el-table-column>
      <el-table-column prop="otherms" label="">
      </el-table-column>
      <el-table-column prop="prtms" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          {{scope.row.call_time.time | dateFormat}}
        </template>
      </el-table-column>
      <el-table-column prop="duration" label="()">
      </el-table-column>
      <el-table-column prop="csName" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tooltip class="item" effect="dark" content="" placement="left-start ">
            <el-button v-if="scope.row.callDuration <= 0" size="small" :plain="true" :disabled="true">
              <i class="fa fa-pause-circle fa-2x" aria-hidden="true"></i>
            </el-button>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="" placement="right-start ">
            <el-button v-if="scope.row.callDuration > 0" size="small" @click="playAudio(scope.$index, scope.row)">
              <i class="fa fa-play-circle fa-2x" aria-hidden="true"></i>
            </el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
    <br>
    <!--   end-->
    <!--   start-->
    <el-row>
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
        </el-pagination>
      </el-col>
    </el-row>
    <!--   end-->
    <!--   start -->
    <el-dialog title="" :visible.sync="dialogAudio" @close="stopAudio">
      <audio autoplay controls onclick="this.play();" id="audio" style="width:100%">
        <source :src="audioSrc" type="audio/mpeg"> 
      </audio>
          <p>
        <a :href="audioSrc" :download="audioSrc">
          <el-button type="primary" size="small" tag="i" class="fa fa-arrow-circle-down">&nbsp;&nbsp;</el-button>
        </a>
      </p>
      <span slot="footer" class="dialog-footer">
          <el-button @click="stopAudio"> </el-button>
          <el-button type="primary" @click="stopAudio"> </el-button>
        </span>
    </el-dialog>
    <!--   end-->
  </div>
</template>
<script>
import { formatDate } from '@/base/date'
import headForm from '@/components/CallRecord/header'

export default {
  components: {
    headForm,
  },
  data() {
    return {
      // 
      respData: [],
      tableLoading: false,
      // 
      currentPage: 1, // 
      pageSize: 5, // 
      totalrecord: 0, // 
      audioSrc: '', // 
      dialogAudio: false,
      changePage: 1, //  
    };
  },
  methods: {
    getTableData(val) {
      this.tableLoading = true;
      val.currentPage = this.changePage //  ;
      this.$axios.post('callRecords_queryCallOutStranger.action','jsonData=' + JSON.stringify(val)).then(response => {
        this.tableLoading = false;
        try {
          let parseData = JSON.parse(response.data)
          let inData = parseData.pageview
          this.respData = inData.records; // 
          this.pageSize = +inData.page_size; // 
          this.totalrecord = +inData.totalrecord; // 
        } catch (e) {
          this.respData = [];
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.tableLoading = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // 
    CurrentPageChange(val) {
      this.changePage = val;
      this.$refs.search.emitTable();
    },
    // 
    export2Excel() {
      require.ensure([], () => {
        const { export_json_to_excel } = require('@/vendor/Export2Excel');
        const tHeader = ["", "", "", "", "", "", ""];
        const filterVal = ["name", "", "otherms", "prtms", "time", "duration", "csName"];
        const data = this.formatJson(filterVal, this.respData);
        export_json_to_excel(tHeader, data, "");
      })
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
    // 
    playAudio(index, row) {
      this.dialogAudio = true;
      // ID
      this.$axios.get('callRecords_audioPlayer.action?callId=' + row.callId).then(response => {
        this.audioSrc = "/5i5j/record/" + response.data;
        // 
        document.getElementById('audio').load()
      })
    },
    // 
    stopAudio() {
      document.getElementById('audio').pause();
      this.audioSrc = "";
      this.dialogAudio = false;
    },
  },
  // --
  filters: {
    dateFormat(time) {
      if (time == undefined) { return ""; }
      var date = new Date(time);
      return formatDate(date, 'yyyy-MM-dd hh:mm');
    }
  },
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
< /style>



