<template>
  <div class="conent-bg">
    <el-row >
      <el-col :offset="2" :span="16" class="panel">
        <el-col :offset="2" :span="7">
          {{userInfo.name}}
        </el-col>
        <el-col :span="7">
          {{userInfo.duty}}
        </el-col>
        <el-col :span="7">
          <el-button type="success" @click="restPwdDialogForm = true"></el-button>
        </el-col>
        <el-col :offset="2" :span="16"><br />
           {{userInfo.orgName}}
        </el-col>
      </el-col>
    </el-row>
    <!--    start -->
    <el-dialog title="" :visible.sync="restPwdDialogForm">
      <el-form :model="restForm" :rules="rules" ref="restForm" label-width="90px">
        <el-form-item label="" prop="oldPwd">
          <el-input v-model.trim="restForm.oldPwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pwd">
          <el-input v-model.trim="restForm.pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pwd2">
          <el-input v-model.trim="restForm.pwd2" type="password"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="restPwdDialogForm = false"> </el-button>
        <el-button type="primary" @click="postPwd">   </el-button>
      </div>
    </el-dialog>
    <!--    end -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      userInfo: {
        name: "test",
        duty: "",
        orgName: "",
      },
      restPwdDialogForm:false,
      // 
      restForm: {
        oldPwd: '',
        pwd: '',
        pwd2: '',
      },
      // --
      rules: {
        oldPwd: { required: true, message: '', trigger: 'blur' },
        pwd: { required: true, message: '', trigger: 'blur' },
        pwd2: { required: true, message: '', trigger: 'blur' },
      },
    }
  },
  methods: {
    getUserInfo() {
      this.$axios.get('sysUser_Info.action').then(response => {
        this.userInfo = JSON.parse(response.data)
      })
    },
    postPwd() {

      this.$refs.restForm.validate((valid) => {
        if (valid) {
          this.restPwdDialogForm = false
          this.$axios.post('sysUser_resetPassword.action','jsonData=' + JSON.stringify(this.restForm)).then(response => {
            console.log(response.data)
            this.$message({ message: "", type: 'success', });
          },response =>{
            this.$message({ message: ""+response, type: 'error', });
          })
        } else {
          this.$message({ message: "", type: 'error', });
          return false;
        }
      });
    }
  },
  created() {
    this.getUserInfo()
  },
};

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.conent-bg {
  background-color: #F2F0F0;
  height: 880px;
  padding-top: 30px;
}
.panel{
  background-color: white;
  padding: 30px 0px;
}
</style>



