<template>
  <div class="conent-bg">
    <el-row>
      <el-col class="box" :offset="1" :span="10">
        <el-card class="box-card">
          <div slot="header">
            <span style="line-height: 36px;"></span>
          </div>
          <ul>
            <li> : {{userInfo.name}} </li>
            <li> : {{userInfo.duty}} </li>
            <li> : {{userInfo.orgName}}
              <router-link v-if="adminPermission" :to="{path: '/5i5j/StaffAdmin'}"></router-link>
            </li>
          </ul>
        </el-card>
      </el-col>
      <el-col class="box" :offset="1" :span="10">
        <el-card class="box-card">
          <div slot="header">
            <span style="line-height: 36px;">
            <router-link v-if="adminPermission" :to="{path: '/5i5j/StaffAdmin?n=third'}"></router-link></span>
          </div>
          <ul>
            <li>  : {{userInfo.total}} &nbsp;&nbsp;&nbsp;&nbsp; : {{userInfo.vtotal}} &nbsp;&nbsp;&nbsp;&nbsp;  : {{userInfo.itotal}}</li>
            <li>  : {{userInfo.ctotal}}</li>
            <!-- <li> : {{userInfo.ototal}}</li> -->
          </ul>
        </el-card>
      </el-col>
    </el-row>
    <el-row>
      <el-col class="box" :offset="1" :span="10">
        <el-card class="box-card">
          <div slot="header">
            <span style="line-height: 36px;"></span>
          </div>
          <ul>
            <li> : {{userInfo.channelCallIn}}</li>
            <!-- <li> : {{userInfo.callOut}}</li> -->
            <li> : {{userInfo.custCallIn}} </li>
          </ul>
        </el-card>
      </el-col>
    </el-row>
    <br />
    <br />
    <br />
    <br />
    <br />
    <br />
    <br />
    <br />
    <br />
    <br />
    <br />
  </div>
</template>
<script>

export default {
  data() {
    return {
      userInfo: {
        total: 0,
        vtotal: 0,
        ctotal: 0,
        duty: "default",
        ototal: 0,
        itotal: 0,
        name: "default",
        channelCallIn: 0,
        orgName: "",
        custCallIn: 0,
        callOut: 0
      },
    }
  },
  methods: {
    getUserInfo() {
      this.$axios.get('overview_overview.action').then(response => {
        try {
          this.userInfo = JSON.parse(response.data);
          // &&
          let adminInfo = JSON.parse(sessionStorage.getItem('loginInfo'));
          this.adminPermission = adminInfo.permission;
        } catch (err) {
          throw "!"
        }
      }, response => {
        this.$message({ message: "" + response, type: "error" })
      })
    },
  },
  created() {
    this.getUserInfo()
  },
};

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.conent-bg {
  background-color: #F2F0F0;
}

.box {
  margin-top: 15px;
  margin-bottom: 30px;
}

.box ul li {
  list-style: none;
  /*letter-spacing: 3px;*/
  line-height: 40px;
}

</style>



