<!--  -->
<template>
  <div>
    <el-form ref="CallLogForm" :inline="true">
      <p>   {{callLogForm.dept}}  <a href="javaScript:void(0)" @click="openTreeDialog"></a> </p>
      <el-form-item>
        <el-select v-model.trim="callLogForm.Channel" clearable placeholder="">
          <el-option v-for="item in ChannelGet" :label="item.label" :value="item.value" :key="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-select v-model.trim="callLogForm.adviser" placeholder="">
          <el-option label="" value="name"></el-option>
          <el-option label="" value="number"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-input v-if="callLogForm.adviser === 'name'" v-model.trim="callLogForm.nameOrUser" placeholder=""></el-input>
        <el-input v-if="callLogForm.adviser === 'number'" v-model.trim="callLogForm.nameOrUser" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-input v-model.trim="callLogForm.CustomerNumber" placeholder=""></el-input>
      </el-form-item>
      <!--   start -->
      <el-form-item label="" tag="div">
        <el-date-picker v-model.trim="dateValue" type="datetimerange" @change="dateChange" placeholder="">
        </el-date-picker>
        <el-checkbox v-model.trim="callLogForm.isDiff"></el-checkbox>
        <el-button type="primary" @click="QueryOutgoing"></el-button>
        <el-button type="primary" :loading="exportLoading" @click="export2file"></el-button>
      </el-form-item>
      <!--   end-->
    </el-form>
    <!--   start  -->
    <tree-menu :dialogState="isTreeDialog" ref="postinfo" @on-close="closeDialog" @post-node="getNodes">
    </tree-menu>
    <!--   end -->
    <!--   start -->
    <el-table ref="multipleTable" v-loading="tableLoading" :data="tableData" border tooltip-effect="dark" style="width: 100%">
      <el-table-column prop="code" label="">
      </el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="channel_name" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tag type="primary">{{scope.row.prtms}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-tag type="gray">{{scope.row.acms}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="otherms" label="">
        <template scope="scope">
          <el-tag type="warning">{{scope.row.otherms}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="call_time" label="" width="150px">
      </el-table-column>
      <template v-hide="!callLogForm.isDiff">
        <el-table-column label="">
          <template scope="scope">{{scope.row.callDuration}} </template>
        </el-table-column>
        <el-table-column prop="csName" label="">
        </el-table-column>
        <el-table-column prop="releaseDir" label="">
        </el-table-column>
        <el-table-column label="" width="80px">
          <template scope="scope">
            <el-tooltip class="item" effect="dark" content="" placement="left-start ">
              <el-button v-if="scope.row.callDuration <= 0" size="small" :plain="true" :disabled="true">
                <i class="fa fa-pause-circle fa-2x" aria-hidden="true"></i>
              </el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="right-start ">
              <el-button v-if="scope.row.callDuration > 0" size="small" @click="playAudio(scope.$index, scope.row)">
                <i class="fa fa-play-circle fa-2x" aria-hidden="true"></i>
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </template>
    </el-table>
    <br>
    <!--   end-->
    <!--   start -->
    <el-row>
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
        </el-pagination>
      </el-col>
    </el-row>
    <!--   end-->
    <!--   start -->
    <el-dialog title="" :visible.sync="export2fileDialog" size="tiny">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="export2fileDialog = false"> </el-button>
        <a :href="fileUrl" target="_blank" :download="fileUrl">
          <el-button type="primary" @click="export2fileDialog = false"> </el-button>
        </a>
      </span>
    </el-dialog>
    <!--   end -->
    <!--   start -->
    <el-dialog title="" :visible.sync="dialogAudio" @close="stopAudio">
      <audio autoplay controls onclick="this.play();" id="audio" style="width:100%">
        <source :src="audioSrc" type="audio/mpeg"> 
      </audio>
      <p>
        <a :href="audioSrc" :download="audioSrc">
          <el-button type="primary" size="small" tag="i" class="fa fa-arrow-circle-down">&nbsp;&nbsp;</el-button>
        </a>
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="stopAudio"> </el-button>
        <el-button type="primary" @click="stopAudio"> </el-button>
      </span>
    </el-dialog>
    <!--   end-->
  </div>
</template>
<script>
import { formatDate } from '@/base/date'
import treeMenu from '@/components/selectSector'

export default {
  components: {
    treeMenu,
  },
  data() {
    return {
      ChannelGet: [],
      // 
      isTreeDialog: false,
      // 
      callLogForm: {
        dept: '',
        deptId: '',
        Channel: '',
        adviser: 'name',
        nameOrUser: '',
        CustomerNumber: '',
        isDiff: false,
        // 
        dateVal: '',
      },
      dateValue: '',
      // 
      audioSrc: '',
      // 
      export2fileDialog: false,
      exportLoading: false,
      fileUrl: '',
      // ()
      tableData: [],
      tableLoading: false,
      dialogAudio: false,
      // --1
      currentPage: 1, // 
      pageSize: 5, // 
      totalrecord: 0, // 
    };
  },
  methods: {
    // 
    getChannel() {
      this.$axios.get('channel_channelList.action').then(response => {
        this.ChannelGet = JSON.parse(response.data)
      })
    },
    // *******  *********
    // 
    openTreeDialog() { // 
      this.isTreeDialog = true;
      this.$refs.postinfo.TreeQuery(); // 
    },
    closeDialog() { // 
      this.isTreeDialog = false;
    },
    getNodes(nodes) {
      this.isTreeDialog = false;
      this.callLogForm.dept = nodes[0].name
      this.callLogForm.deptId = nodes[0].value
    },
    /*************  **************/
    // 
    dateChange(val) {
      this.callLogForm.dateVal = val;
    },
    // 
    QueryOutgoing() {
      this.callLogForm.currentPage = this.currentPage;
      this.getOutgoingTable()
    },
    // 1
    CurrentPageChange(val) {
      this.callLogForm.currentPage = val;
      this.getOutgoingTable()
    },
    // 
    getOutgoingTable() {
      this.tableLoading = true;
      this.$axios.post('callRecords_queryCallInList.action', 'jsonData=' + JSON.stringify(this.callLogForm)).then(response => {
        this.tableLoading = false;
        try {
          console.table(JSON.parse(response.data))
          let outgoingFmt = JSON.parse(response.data)
          let outgoingData = outgoingFmt.pageview
          this.tableData = outgoingData.records; // 
          this.pageSize = +outgoingData.page_size; // 
          this.totalrecord = +outgoingData.totalrecord; // 
        } catch (e) {
          this.tableData = [];
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.tableLoading = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // 
    export2file() {
      this.exportLoading = true;
      this.$axios.post('callRecords_callInListExportExcel.action', 'jsonData=' + JSON.stringify(this.callLogForm)).then(response => {
        var res = JSON.parse(response.data)
        if (res.result == 'success') {
          this.fileUrl = res.filepath;
          this.export2fileDialog = true;
        } else {
          this.$message({ message: res.cause, type: 'error' });
        }
        this.exportLoading = false;
      }, response => {
        this.exportLoading = false;
        this.$message({ message: "" + response, type: 'error' });
      })
    },
    // 
    playAudio(index, row) {
      this.dialogAudio = true;
      // ID
      this.$axios.get('callRecords_audioPlayer.action?callId=' + row.callId).then(response => {
        this.audioSrc = "/5i5j/record/" + response.data;
        // 
        document.getElementById('audio').load()
      })
    },
    // 
    stopAudio() {
      document.getElementById('audio').pause();
      this.audioSrc = "";
      this.dialogAudio = false;
    },
  },
  created() {
    this.getChannel()
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



