<template>
  <div>
    <el-row>
      <el-col :offset="1" :span="22">
        <h3></h3>
        <el-tabs v-model.trim="activeName" type="card" @tab-click="tabClick">
          <el-tab-pane label="" name="first">
            <p>   {{OnHookForm.dept}}  <a href="javaScript:void(0)" @click="openTreeDialog"></a> </p>
            <el-form ref="OnHokForm" :inline="true">
              <el-form-item>
                <el-select v-model.trim="OnHookForm.state" clearable placeholder="">
                  <el-option label="" value="S"></el-option>
                  <el-option label="" value="F"></el-option>
                </el-select>
                <el-select v-model.trim="OnHookForm.AllScenes" clearable placeholder="">
                  <el-option v-for="item in scenesGet" :label="item.templateName" :value="item.templateName" :key="item.templateId"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-input v-model.trim="OnHookForm.userName" placeholder=""></el-input>
              </el-form-item>
              <el-form-item>
                <el-input v-model.trim="OnHookForm.targetNumber" placeholder=""></el-input>
              </el-form-item>
              <!--   start-->
              <el-form-item tag="div">
                <el-date-picker v-model.trim="dateValue" type="datetimerange" @change="dateChange" placeholder="">
                </el-date-picker>
                <el-button type="primary" @click="QueryNotifyRecord"></el-button>
                <el-button type="primary" :loading="exportLoading" @click="export2file"></el-button>
              </el-form-item>
              <!--   end-->
            </el-form>
            <!--   start -->
            <tree-menu :dialogState="isTreeDialog" ref="postinfo" @on-close="closeDialog" @post-node="getNodes">
            </tree-menu>
            <!--   end -->
            <!-- 1  start-->
            <el-table ref="multipleTable" v-loading="tableLoading" :data="tableData" border tooltip-effect="dark" style="width: 100%">
              <el-table-column prop="timestamp" label="" width="200px">
              </el-table-column>
              <el-table-column prop="targetNumber" label="">
              </el-table-column>
              <el-table-column label="">
                <template scope="scope">
                  <el-tag v-if="scope.row.state ==='S'" type="success"></el-tag>
                  <el-tag v-if="scope.row.state !=='S'" type="danger"></el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="scenario" label="">
              </el-table-column>
              <el-table-column prop="content" :show-overflow-tooltip="true" label="">
              </el-table-column>
              <el-table-column prop="cntsms" label="()">
              </el-table-column>
              <el-table-column prop="userName" label="">
              </el-table-column>
              <el-table-column label="">
                <template scope="scope">
                  <el-button size="small" type="text" @click="handleSee(scope.$index, scope.row)"></el-button>
                </template>
              </el-table-column>
            </el-table>
            <br>
            <!-- 1  end-->
            <!--   start -->
            <el-dialog title="" :visible.sync="export2fileDialog" size="tiny">
              <span></span>
              <span slot="footer" class="dialog-footer">
                <el-button @click="export2fileDialog = false"> </el-button>
                <a :href="fileUrl" target="_blank" :download="fileUrl">
                  <el-button type="primary" @click="export2fileDialog = false"> </el-button>
                </a>
              </span>
            </el-dialog>
            <!--   end -->
            <!--   start-->
            <el-row>
              <el-col :offset="8">
                <el-pagination @size-change="currentPage" @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
                </el-pagination>
              </el-col>
            </el-row>
            <!--   end-->
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <p>Dialog</p>
            <el-button>Open Dialog</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { formatDate } from '@/base/date'
import treeMenu from '@/components/selectSector'

export default {
  components: {
    treeMenu,
  },
  data() {
    return {
      activeName: 'first',
      // 
      scenesGet: [],
      // 
      OnHookForm: {
        dept: '',
        deptId: '',
        userName: '',
        AllScenes: '',
        targetNumber: '',
        state: '',
        dateVal: '',
      },
      // 
      isTreeDialog: false,
      // 
      dateValue: '',
      // 
      export2fileDialog: false,
      exportLoading: false,
      fileUrl: '',
      // 
      tableData: [],
      tableLoading: false,
      dialogVisible: false,
      // --1
      currentPage: 1, // 
      pageSize: 5, // 
      totalrecord: 0, // 
    };
  },
  methods: {
    tabClick(tab, event) {
      // console.log(tab, event);
    },
    // *******  *********
    // 
    openTreeDialog() { // 
      this.isTreeDialog = true;
      this.$refs.postinfo.TreeQuery(); // 
    },
    closeDialog() { // 
      this.isTreeDialog = false;
    },
    getNodes(nodes) {
      this.isTreeDialog = false;
      this.OnHookForm.dept = nodes[0].name
      this.OnHookForm.deptId = nodes[0].value
    },
    // 
    getScenes() {
      this.$axios.get('template_getfindNotificationScenario.action').then(response => {
        console.table(JSON.parse(response.data))
        this.scenesGet = JSON.parse(response.data)
      })
    },
    // 1
    dateChange(val) {
      this.OnHookForm.dateVal = val;
    },
    // 
    QueryNotifyRecord() {
      this.OnHookForm.currentPage = this.currentPage;
      this.getnotifyRecordTable()
    },
    // 1
    CurrentPageChange(val) {
      this.OnHookForm.currentPage = val;
      this.getnotifyRecordTable()
    },
    // 
    getnotifyRecordTable() {
      this.tableLoading = true;
      console.log(JSON.stringify(this.OnHookForm))
      this.$axios.post('notifyRecord_getfindRecord.action','jsonData=' + JSON.stringify(this.OnHookForm)).then(response => {
        this.tableLoading = false;
        console.log(response.data)
        try {
          let notifyRecordFmt = JSON.parse(response.data)
          let notifyRecordData = notifyRecordFmt.pageview
          this.tableData = notifyRecordData.records; // 
          this.pageSize = +notifyRecordData.page_size; // 
          this.totalrecord = +notifyRecordData.totalrecord; // 
        } catch (e) {
          this.tableData = [];
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.tableLoading = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // 
    export2file() {
      this.exportLoading = true;
      this.$axios.post('notifyRecord_notifyRecordExportExcel.action', 'jsonData=' + JSON.stringify(this.OnHookForm)).then(response => {
        var res = JSON.parse(response.data)
        if (res.result == 'success') {
          this.fileUrl = res.filepath;
          this.export2fileDialog = true;
        } else {
          this.$message({ message: res.cause, type: 'error' });
        }
        this.exportLoading = false;
      }, response => {
        this.exportLoading = false;
        this.$message({ message: "" + response, type: 'error' });
      })
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
    // 
    handleSee(i, row) {
      console.log(row.callId)
      this.$axios.get('notifyRecord_getfindRecordMore.action?id=' + row.callId).then(response => {
        try {
          let resData = JSON.parse(response.data),
              date1 = new Date(resData.callTime.time),
              callTime = formatDate(date1, 'yyyy-MM-dd hh:mm'),
              date2 = new Date(resData.releaseTime.time),
              releaseTime = formatDate(date2, 'yyyy-MM-dd hh:mm');
          const h = this.$createElement;
          this.$msgbox({
            title: '',
            message: h('ul', { style: 'line-height:50px;' }, [
                      h('li', null, '' + resData.userName),
                      h('li', null, '' + resData.prtms),
                      h('li', null, '' + resData.otherms),
                      h('li', null, '' + resData.acms),
                      h('li', null, '' + callTime),
                      h('li', null, '' + releaseTime),
                    ]),
            showCancelButton: true,
            confirmButtonText: '',
            cancelButtonText: '',
            beforeClose: (action, instance, done) => { done(); }
          })
        } catch (e) {
          this.$message({ message: "", type: 'error', });
        }
      })
    },
  },
  created() {
    this.getScenes();
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



