<template>
  <div>
    <el-button type="primary" @click="pushTpl" v-if="adminPermission"></el-button>
    <el-button type="primary" @click="updataTpl"></el-button>
    <a href="javascript:void(0);" style="color:#FF4949;" @click="sindInfo"> </a>
    <a href="javascript:void(0);" style="color:#FF4949;" @click="explainInfo"> </a>
    <!--  start-->
    <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" v-loading="tableLoading" element-loading-text="" style="width: 100%">
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="content" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <span v-if="scope.row.status==='c'"></span>
          <span v-if="scope.row.status==='w'"></span>
          <span v-if="scope.row.status==='f'"></span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">{{ scope.row.type==='u' ?'':'' }}</template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button size="small" type="warning" @click="clickEditTpl(scope.$index, scope.row)"></el-button>
          <el-button size="small" type="danger" @click="DelTpl(scope.$index, scope.row)" v-if="adminPermission"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  end-->
    <!--   start-->
    <el-row>
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
        </el-pagination>
      </el-col>
    </el-row>
    <!--   end-->
    <!--  start-->
    <el-dialog title=":" :visible.sync="dialogTpl">
      <el-form :model="TplForm">
        <el-form-item label="">
          <el-input v-model.trim="TplForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" :rows="2" v-model.trim="TplForm.content" auto-complete="off"></el-input>
          <p style="color: #FF4949">{{TplForm.content.length}}/70</p>
        </el-form-item>
        <el-form-item>
          <a href="javascript:void(0);" @click="addInfo('#Agent#')"></a>
          <a href="javascript:void(0);" @click="addInfo('#Phone#')"></a>
          <a href="javascript:void(0);" @click="addInfo('#CustomerNo#')"></a>
          <!-- <a href="javascript:void(0);" @click="addInfo('#CustomerId#')"></a> -->
          <!-- <a href="javascript:void(0);" @click="addInfo('#ProductId#')"></a> -->
          <!-- <a href="javascript:void(0);" @click="addInfo('#Time#')"></a> -->
          <!-- <a href="javascript:void(0);" @click="addInfo('#VirtualNo#')"></a> -->
          <a href="javascript:void(0);" @click="addInfo('#Channel#')"></a>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogTpl = false"> </el-button>
        <el-button type="primary" @click="EditTplTable()"> </el-button>
      </div>
    </el-dialog>
    <!--  end-->
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      tableData: [],
      tableLoading: false,
      dialogTpl: false,
      isEditPush: 0, // 
      TplForm: { // 
        name: '',
        content: '',
      },
      // 
      currentPage: 1, // 
      pageSize: 5, // 
      totalrecord: 0, // 
        // 
      adminPermission:''
    };
  },
  methods: {
    // ***** 
    // 
    pushTpl() { // dialog
      this.dialogTpl = true;
      this.isEditPush = 0;
      // 
      this.TplForm = {
        name: '',
        content: '',
      };
    },
    // 
    updataTpl() {
      this.tableLoading = true;
      this.$axios.get('template_getfindTemplate.action?currentPage=' + this.currentPage).then(response => {
        this.tableLoading = false;
         try {
            let parseData = JSON.parse(response.data)
            let inData = parseData.pageview
            this.tableData = inData.records; // 
            this.pageSize = +inData.page_size; // 
            this.totalrecord = +inData.totalrecord; // 
          } catch (e) {
          this.tableData = [];
          this.$message({ message: "", type: 'error' });
        }
      }, response => {
        this.tableLoading = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // 
    CurrentPageChange(val) {
      this.tableLoading = true;
      this.currentPage = val;
      this.updataTpl()
    },
    // 
    clickEditTpl(i, row) { // dialog
      this.dialogTpl = true;
      this.isEditPush = 1;
      this.TplForm.templateId = row.templateId
      this.TplForm.name = row.name
      this.TplForm.content = row.content
    },
    // 
    EditTplTable() {
      console.log(JSON.stringify(this.TplForm))
      let url = this.isEditPush === 1 ? "template_update.action" : "template_add.action";

      this.$axios.post(url, 'jsonData=' + JSON.stringify(this.TplForm)).then(response => {
        this.dialogTpl = false;
        // 
        this.TplForm = { name: '', content: '', };
        this.updataTpl()
        this.isEditPush === 1 ?
          this.$message({ message: "", type: 'success', }) :
          this.$message({ message: "", type: 'success', })

      }, response => {
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // 
    DelTpl(i, row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        this.$axios.get("template_ delete.action?templateId=" + row.templateId).then(response => {
          this.$message({ message: "", type: 'success' });
          this.updataTpl();
        }, response => {
          this.$message({ message: "" + response, type: 'error' })
        })

      }).catch(() => {
        this.$message({ message: '', type: 'info', });
      });
    },
    // 
    addInfo(info) {
      this.TplForm.content += info;
    },
    // 
    sindInfo() {
      const h = this.$createElement;
      this.$msgbox({
        title: '',
        message: h('ol', { style: 'line-height:30px;' }, [
          h('li', null, '30'),
          h('li', null, '53'),
          h('li', null, '24'),
          h('li', null, '1'),
          h('li', { style: 'color: red' }, ' 70 ')
        ]),
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
        beforeClose: (action, instance, done) => { done(); }
      })
    },
    // 
    explainInfo() {
      const h = this.$createElement;
      this.$msgbox({
        title: '',
        message: h('ul', { style: 'line-height:30px;' }, [
          h('li', null, '#Agent#, ,  '),
          h('li', null, '#Phone#, , '),
          h('li', null, '#CustomerNo#,,'),
          // h('li', null, '#CustomerId#, , '),
          // h('li', null, '#ProductId#, , '),
          // h('li', null, '#Time#, '),
          // h('li', null, '#VirtualNo#, (,),'),
          h('li', null, '#Channel#, , '),
          // h('li', null, '#Phone#, , , , ')
        ]),
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
        beforeClose: (action, instance, done) => { done(); }
      })
    },
    isAdmin(){
     // &&
    let adminInfo = JSON.parse(sessionStorage.getItem('loginInfo'));
    this.adminPermission = adminInfo.functionpermission;
    }
  },
  created() {
    this.isAdmin()
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



