<template>
  <div>
    <el-row>
      <el-col :offset="1" :span="20">
        <h3></h3>
        <el-tabs v-model.trim="activeName" type="card" @tab-click="tabchange">
          <el-tab-pane label="" name="first">
            <el-button type="primary" @click="AddTableList"></el-button>
            <!--  start-->
            <el-table ref="multipleTable" :data="tableData1" border tooltip-effect="dark" v-loading="tableLoading1" element-loading-text="" style="width: 100%">
              <el-table-column prop="channelName" label="">
              </el-table-column>
              <el-table-column prop="count" label="">
              </el-table-column>
              <el-table-column label="">
                <template scope="scope">
                  <el-button size="small" type="danger" @click="DelTableList(scope.$index, scope.row)"></el-button>
                </template>
              </el-table-column>
            </el-table>
            <br />
            <!--  end-->
            <!--   start-->
            <el-row>
              <el-col :offset="8">
                <el-pagination @current-change="CurrentPageChange1" :current-page.sync="currentPage1" :page-size="pageSize1" layout="prev, pager, next, jumper,total" :total="totalrecord1">
                </el-pagination>
              </el-col>
            </el-row>
            <!--   end-->
          </el-tab-pane>
          <!--         <el-tab-pane label="" name="second">
            <el-form ref="NumberDisplayform" label-width="150px">
              <el-form-item label="">
                <el-select v-model.trim="NumberDisplay.id101" placeholder="">
                  <el-option label="" value="T"></el-option>
                  <el-option label="" value="P"></el-option>
                  <el-option label="" value="N"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model.trim="NumberDisplay.id102" placeholder="">
                  <el-option label="" value="T"></el-option>
                  <el-option label="" value="P"></el-option>
                  <el-option label="" value="N"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model.trim="NumberDisplay.id103" placeholder="">
                  <el-option label="" value="T"></el-option>
                  <el-option label="" value="P"></el-option>
                  <el-option label="" value="N"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model.trim="NumberDisplay.id104" placeholder="">
                  <el-option label="" value="T"></el-option>
                  <el-option label="" value="P"></el-option>
                  <el-option label="" value="N"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label-width="150px">
                <el-button type="primary" @click="keepNumberDisplay"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane> -->
          <el-tab-pane label="" name="third">
            <el-form label-width="120px">
              <el-form-item label="">
                <el-select @change="CLchangeCity" v-model.trim="city" placeholder="">
                  <el-option v-for="item in CityGet" :label="item.descrShort" :value="item.setId" :key="item.setId">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item v-for="(items,index) in CLOption.parameterRing" :label="items.item" :key="index">
                <el-select v-model.trim="items.rid" placeholder="">
                  <el-option v-for="item in CLSelect" :label="item.rname" :value="item.rid" :key="item.rid">
                  </el-option>
                </el-select>
                <a v-for="item in CLSelect" v-if="items.rid === item.rid" :href="item.url">
                  <el-button type="primary" size="small" tag="i" class="fa fa-arrow-circle-down"></el-button>
                </a>
              </el-form-item>
              <el-form-item v-for="(items,index) in CLOption.channelRing" :key="index">
                <span>{{ items.channelName }}</span>
                <br />
                <el-select v-model.trim="items.rid" placeholder="">
                  <el-option v-for="item in CLSelect" :label="item.rname" :value="item.rid" :key="item.rid">
                  </el-option>
                </el-select>
                <a v-for="item in CLSelect" v-if="items.rid === item.rid" :href="item.url" target="_blank" :download="item.url">
                  <el-button type="primary" size="small" tag="i" class="fa fa-arrow-circle-down"></el-button>
                </a>
              </el-form-item>
              <el-form-item label-width="150px">
                <el-button type="primary" @click="keepCL"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="" name="fourth">
            <el-form ref="form" label-width="150px">
              <el-form-item label="">
                <el-select @change="SMSchangeCity" v-model.trim="city" placeholder="">
                  <el-option v-for="item in CityGet" :label="item.descrShort" :value="item.setId" :key="item.setId">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <span></span>
                <el-select v-model="SmsOnHookOption.id108" clearable placeholder="">
                  <el-option v-for="item in SmsOnHookForm" :label="item.templateName" :value="item.templateId" :key="item.templateId"></el-option>
                </el-select>
                <span>&nbsp;&nbsp;</span>
                <el-select v-model="SmsOnHookOption.id109" clearable placeholder="">
                  <el-option v-for="item in SmsOnHookForm" :label="item.templateName" :value="item.templateId" :key="item.templateId"></el-option>
                </el-select>
                  <span>&nbsp;&nbsp;</span>
                  <el-select v-model="SmsOnHookOption.id110" clearable placeholder="">
                    <el-option v-for="item in SmsOnHookForm" :label="item.templateName" :value="item.templateId" :key="item.templateId"></el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="">
                <span></span>
                <el-select v-model="SmsOnHookOption.id111" clearable placeholder="">
                  <el-option v-for="item in SmsOnHookForm" :label="item.templateName" :value="item.templateId" :key="item.templateId"></el-option>
                </el-select>
                <span>&nbsp;&nbsp;</span>
                <el-select v-model="SmsOnHookOption.id113" clearable placeholder="">
                  <el-option v-for="item in SmsOnHookForm" :label="item.templateName" :value="item.templateId" :key="item.templateId"></el-option>
                </el-select>
                  <span>&nbsp;&nbsp;</span>
                  <el-select v-model="SmsOnHookOption.id112" clearable placeholder="">
                    <el-option v-for="item in SmsOnHookForm" :label="item.templateName" :value="item.templateId" :key="item.templateId"></el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label-width="150px">
                <el-button type="primary" @click="keepSO"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeName: 'first',
      //****** ()
      // 
      tableData1: [],
      tableLoading1: false,
      // 
      currentPage1: 1, // 
      pageSize1: 5, // 
      totalrecord1: 0, // 
      // ****** ()
      NumberDisplay: {
        id101: '', // 
        id102: '', // 
        id103: '', // 
        id104: '', // 
      },
      // 
      CityGet: [],
      city: "", // 
      // ****** 
      CLOption: [],
      CLSelect: [],
      CLsetId: "",
      // ***** 
      SmsOnHookForm: [],
      SmsOnHookOption: {},
      SmsSetId: "",
    }
  },
  methods: {
    // ***** 
    // 
    queryChannel() {
      this.currentPage1 = 1;
      this.getTabelList()
    },
    // 
    CurrentPageChange1(val) {
      this.currentPage1 = val;
      this.getTabelList()
    },
    getTabelList() {
      this.tableLoading1 = true;
      this.$axios.get('channel_getChannelList.action?currentPage=' + this.currentPage1).then(response => {
        this.tableLoading1 = false;
        try {
          console.table(JSON.parse(response.data))
          let funFmt = JSON.parse(response.data)
          let funPage = funFmt.pageview;
          this.pageSize1 = +funPage.page_size; // 
          this.totalrecord1 = +funPage.totalrecord; // 
          this.tableData1 = funPage.records; // 
        } catch (e) {
          this.tableData1 = [];
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.tableLoading1 = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // 
    AddTableList() {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /(a-z|A-Z|\w|\d|[\u4e00-\u9fa5]){1,12}/,
        inputErrorMessage: '',
      }).then(({ value }) => {
        // 
        this.$axios.get('channel_add.action?channelName=' + value).then(response => {
          this.$message({ message: "", type: 'success', });
          this.getTabelList()
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // 
    DelTableList(index, row) {
      this.$axios.get('channel_delete.action?Id=' + row.channelId).then(response => {
        this.$message({ message: "", type: 'success', });
        this.getTabelList()
      })
    },
    // ***** 
    // 
    keepNumberDisplay() {
      let keepND = JSON.stringify(this.NumberDisplay)
      this.$axios.get('parameter_addCallInNumber.action?jsonData=' + keepND).then(response => {
        this.$message({ message: "", type: 'success' });
      }, response => {
        this.$message({ message: "" + response, type: "error" });
      })
    },
    // 
    getNumberDisplay() {
      this.$axios.get('parameter_getCallInNumber.action').then(response => {
        this.NumberDisplay = JSON.parse(response.data);
      })
    },
    // ***** 
    // 
    getCity() {
      this.$axios.get('role_showCollectionIdBySetId.action').then(response => {
        this.CityGet = JSON.parse(response.data)
      })
    },
    // 
    CLchangeCity(val) {
      this.CLsetId = val
      this.getCLForm()
    },
    // 
    getCLForm() {
      // 
      this.$axios.get('parameter_getCallInMusic.action?setId=' + this.CLsetId).then(response => {
        this.CLOption = JSON.parse(response.data)
      }, response => {
        this.$message({ message: "" + response, type: "error" });
      })
      // 
      this.$axios.get('ringtone_callInfindRingtone.action').then(response => {
        this.CLSelect = JSON.parse(response.data)
      }, response => {
        this.$message({ message: "" + response, type: "error" });
      })
    },
    // 
    keepCL() {
      this.CLOption.setId = this.CLsetId
      console.log(JSON.stringify(this.CLOption))
      this.$axios.post('parameter_addCallInMusic.action','jsonData=' + JSON.stringify(this.CLOption)).then(response => {
        if (response.data === "success") {
          this.$message({ message: "", type: 'success' });
        }else{
          this.$message({ message: "", type: "error" });
        }
      }, response => {
        this.$message({ message: "" + response, type: "error" });
      })
    },
    // ***** 
    // 
    SMSchangeCity(val) {
      this.SmsSetId = val
      this.getSMSForm();
    },
    // 
    getSMSForm() {
      // 
      this.$axios.get('parameter_getCallInMessage.action?setId=' + this.SmsSetId).then(response => {
        this.SmsOnHookOption = JSON.parse(response.data);
      }, response => {
        this.$message({ message: "" + response, type: "error" });
      })
      // 
      this.$axios.get('template_getfindNotificationScenario.action').then(response => {
        this.SmsOnHookForm = JSON.parse(response.data);
      }, response => {
        this.$message({ message: "" + response, type: "error" });
      })
    },
    // 
    keepSO() {
      this.SmsOnHookOption.setId = this.CLsetId
      this.$axios.get('parameter_addCallInMessage.action?jsonData=' + JSON.stringify(this.SmsOnHookOption)).then(response => {
        if (response.data === "success") {
          this.$message({ message: "", type: 'success' });
        }else{
          this.$message({ message: "", type: "error" });
        }
      }, response => {
        this.$message({ message: "" + response, type: "error" });
      })
    },
    // tab
    tabchange(tab, event) {
    },
  },
  created() {
    this.queryChannel();
    this.getCity()
    // this.getNumberDisplay()  
  },
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



