
<!--  -->
<template>
  <div>
    <p>   {{staffForm.org}}  <a href="javaScript:void(0)" @click="openTreeDialog('search')"></a> </p>
    <!--   start -->
    <el-form ref="staffform">
      <el-form-item>
        <el-select v-model.trim="staffForm.channel" clearable placeholder="">
          <el-option v-for="item in ChannelGet" :label="item.label" :value="item.value" :key="item.value">
          </el-option>
        </el-select>
        <el-select v-model.trim="staffForm.duty" clearable placeholder="">
          <el-option v-for="item in dutyGet" :label="item.label" :value="item.label" :key="item.value">
          </el-option>
        </el-select>
        <el-select v-model.trim="staffForm.state" placeholder="">
          <el-option label="" value="all"></el-option>
          <el-option label="" value="V"></el-option>
          <el-option label="" value="I"></el-option>
        </el-select>
        <el-select v-model.trim="staffForm.power" placeholder="">
          <el-option label="" value="all"></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <el-row>
      <el-col :span="5">
        <el-select v-model.trim="staffForm.Name" placeholder="">
          <el-option label="" value="name"></el-option>
          <el-option label="" value="number"></el-option>
        </el-select>
      </el-col>
      <el-col :span="5">
        <el-input v-model.trim="staffForm.nameOrNum" placeholder=""></el-input>
      </el-col>
      <el-col :span="5">
        <el-button type="primary" @click="queryStaff"></el-button>
        <el-dropdown>
          <el-button type="primary">
            <i class="el-icon-caret-bottom el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown" style="">
            <el-dropdown-item>
              <p @click="freeze"></p>
            </el-dropdown-item>
            <el-dropdown-item>
              <p @click="unfreeze"></p>
            </el-dropdown-item>
            <el-dropdown-item>
              <p @click="unAllBind"></p>
            </el-dropdown-item>
            <el-dropdown-item>
              <p @click="AllDel"></p>
            </el-dropdown-item>
            <el-dropdown-item>
              <p @click="changeDept"></p>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
      <el-col :offset="1" :span="8">
        <el-button type="primary" @click="AddStaffDialog = true"></el-button>
        <el-button type="primary" :loading="exportLoading" @click="export2file"></el-button>
        <el-button type="primary" :loading="dialogLoading" @click="dialogImportFile = true"></el-button>
      </el-col>
    </el-row>
    <br>
    <!--   end -->
    <!--   start -->
    <el-dialog title="" :visible.sync="export2fileDialog" size="tiny">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="export2fileDialog = false"> </el-button>
        <a :href="fileUrl" target="_blank" :download="fileUrl">
          <el-button type="primary" @click="export2fileDialog = false"> </el-button>
        </a>
      </span>
    </el-dialog>
    <!--   end -->
    <!--   start-->
    <el-dialog title="" :visible.sync="dialogImportFile">
      <el-form :model="ImportFileData">
        <el-form-item label="">
          <el-upload ref="ImportFile" action="/5i5j/staff_importExcel.action" accept="text/csv" :on-success="flieSuccess" :on-error="fileError" :file-list="fileList" name="staffFile" :auto-upload="false">
            <el-button type="primary" icon="upload"></el-button>
            <div slot="tip" class="el-upload__tip"></div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogImportFile = false"> </el-button>
        <el-button type="primary" @click="ImportFileOk"> </el-button>
      </div>
    </el-dialog>
    <!--   end-->
    <!--  start-->
    <el-table ref="multipleTable" v-loading="tableLoading" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="tableSelectionChange">
      <el-table-column type="selection" width="50">
      </el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="duty" label="">
      </el-table-column>
      <el-table-column prop="code" label="">
      </el-table-column>
      <el-table-column prop="org_name" label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-popover trigger="hover" placement="top">
            <template v-if="scope.row.subrelas.length === 0">
              <el-tag type="danger">     :)</el-tag>
            </template>
            <template v-if="scope.row.subrelas.length >= 1" v-for="item in scope.row.subrelas">
              <p>
                <el-tag :type="'success'" close-transition>{{item.channelName}}</el-tag>
                <el-tag :type="'primary'" close-transition>{{item.anum}} </el-tag>
                <el-tag :type="'primary'" close-transition>{{item.xnum}} </el-tag>
                <el-tag :type="'warning'" close-transition>{{item.upline}}</el-tag>
              </p>
            </template>
            <div slot="reference">
              <el-tag :class="{ 'active-wrapper': scope.row.subrelas.length >= 1}"></el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          {{ scope.row.data_permission===1 ?'':'' }}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">{{ scope.row.status ==='V' ?'':'' }}</template>
      </el-table-column>
      <el-table-column label="" width="260">
        <template scope="scope">
          <el-button type="success" size="mini" @click="openEditStaff(scope.$index, scope.row)"></el-button>
          <el-button type="warning" size="mini" @click="unBind(scope.$index, scope.row)"></el-button>
          <el-button type="info" size="mini" @click="openChangeBind(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"></el-button>
          <!-- <el-button type="primary" size="mini" @click="resetPwd(scope.$index, scope.row)"></el-button> -->
        </template>
      </el-table-column>
    </el-table>
    <br />
    <!--  end-->
    <!--   start-->
    <el-row>
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper, total" :total="totalrecord">
        </el-pagination>
      </el-col>
    </el-row>
    <!--   end-->
    <!--   start -->
    <el-dialog title="" :visible.sync="AddStaffDialog" @close="closeTreeInfo">
      <el-form :model="AddStaffV" :inline="true">
        <el-form-item label="">
          <el-input v-model.trim="AddStaffForm.code" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-checkbox v-model.trim="AddStaffForm.power" :true-label="1" :false-label="0"></el-checkbox>
          <el-checkbox v-model.trim="AddStaffForm.frozen" true-label="I" false-label="V"></el-checkbox>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="AddStaffForm.name" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model.trim="AddStaffForm.duty" placeholder="">
            <el-option v-for="item in dutyGet" :label="item.label" :value="item.label" :key="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="AddStaffForm.idcard" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
           {{AddStaffForm.org}} 
          <el-button auto-complete="off" @click="openTreeDialog('add')"></el-button>
        </el-form-item>
        <el-form-item label="">
          <el-button auto-complete="off" icon="plus" @click="AddRow(AddStaffForm.numberList)"></el-button>
          <span style="color:gray">--</span>
        </el-form-item>
        <!--   start -->
        <el-form-item v-for="(items,index) in AddStaffForm.numberList" :key="index">
          <i class="el-icon-close" @click="delRow(AddStaffForm.numberList,items)"></i>
          <el-select style="width:160px;" v-model.trim="items.channelVal" placeholder="">
            <el-option v-for="(item,index) in items.channel" :value="item.value" :label="item.label" :key="index"></el-option>
          </el-select> -
          <el-input v-model.trim="items.master" style="width:160px;" placeholder=""></el-input> -
          <el-input v-model.trim="items.trumpet" style="width:160px;" placeholder=""></el-input> -
          <!-- <el-checkbox v-model.trim="items.upline"></el-checkbox> -->
        </el-form-item>
        <!--   end -->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="AddStaffDialog = false"> </el-button>
        <el-button type="primary" @click="AddStaff"> </el-button>
      </div>
    </el-dialog>
    <!--   end -->
    <!--   start -->
    <el-dialog title="" :visible.sync="EditStaffDialog" @close="closeTreeInfo">
      <el-form :model="EditStaffV" :inline="true">
        <el-form-item label="">
          <el-input v-model.trim="EditStaffForm.code" auto-complete="off" :disabled="true" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-checkbox v-model.trim="EditStaffForm.power" :true-label="1" :false-label="0"></el-checkbox>
          <el-checkbox v-model.trim="EditStaffForm.frozen" true-label="I" false-label="V"></el-checkbox>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="EditStaffForm.name" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model.trim="EditStaffForm.duty" placeholder="">
            <el-option v-for="item in dutyGet" :label="item.label" :value="item.label" :key="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="EditStaffForm.idcard" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
           {{EditStaffForm.org}} 
          <el-button auto-complete="off" @click="openTreeDialog('edit')"></el-button>
        </el-form-item>
        <el-form-item label="">
          <el-button auto-complete="off" icon="plus" @click="AddRow(EditStaffForm.numberList)"></el-button>
          <span style="color:gray">--</span>
        </el-form-item>
        <!--   start -->
        <el-form-item v-for="(items,index) in EditStaffForm.numberList" :key="index">
          <i class="el-icon-close" :disabled="items.isDisabled" @click="delRow(EditStaffForm.numberList,items)"></i>
          <el-select style="width:160px;" v-model.trim="items.channelVal" :disabled="items.isDisabled" placeholder="">
            <el-option v-for="(item,index) in items.channel" :value="item.value" :label="item.label" :key="index"></el-option>
          </el-select> -
          <el-input v-model.trim="items.master" :disabled="items.isDisabled" style="width:160px;" placeholder=""></el-input> -
          <el-input v-model.trim="items.trumpet" :disabled="items.isDisabled" style="width:160px;" placeholder=""></el-input> -
          <!-- <el-checkbox v-model.trim="items.upline" :disabled="items.isDisabled"></el-checkbox> -->
        </el-form-item>
        <!--   end -->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="EditStaffDialog = false"> </el-button>
        <el-button type="primary" @click="enterEditStaff"> </el-button>
      </div>
    </el-dialog>
    <!--   end -->
    <!--    start-->
    <el-dialog title="" :visible.sync="changeBindDialog">
      <el-form :model="changeBindDialogFrom">
        <el-form-item label="">
          <el-input v-model.trim="changeBindDialogFrom.code" :disabled="true" style="width:300px;" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" v-for="(item,index) in changeBindDialogFrom.subrelas" :key="index">
          <el-checkbox v-model.trim="item.isChange"></el-checkbox>
          <el-input v-model.trim="item.channelName" :disabled="true" style="width:160px;"></el-input>
          <el-input v-model.trim="item.anum" style="width:160px;" placeholder=""></el-input>
          <el-input v-model.trim="item.xnum" :disabled="true" style="width:160px;"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="changeBindDialog = false"> </el-button>
        <el-button type="primary" @click="enterChangeBind"> </el-button>
      </div>
    </el-dialog>
    <!--   end-->
   <!-- ()  start -->
    <tree-menu :dialogState="isTreeDialog" ref="postinfo" @on-close="closeDialog" @post-node="getNodes"></tree-menu>
    <!-- ()  end -->
    <!-- ()  start -->
    <tree-menu :dialogState="isChangeDeptTreeDialog" ref="postChangeDept" @on-close="closeDialog" @post-node="getChangeDeptNodes"></tree-menu>
    <!-- ()  end -->
  </div>
</template>
<script>
import treeMenu from '@/components/selectSector'
import bus from '@/bus' //
export default {
  components: {
    treeMenu,
  },
  data() {
    return {
      activeName: 'first', // tabtab-name
      ChannelGet: [], // 
      dutyGet: [], // 
      // 
      isTreeDialog: false,
      // 
      fileUrl: '',
      // 
      export2fileDialog: false,
      //  &&&& 
      isSearchAddEditTree: '',
      // 
      staffForm: {
        org: '',
        orgId: '',
        duty: "",
        power: 'all',
        state: 'all',
        channel: '',
        Name: 'name',
        nameOrNum: '',
      },
      // 
      tableData: [],
      // loding
      exportLoading: false,
      tableLoading: false,
      // 
      AddStaffDialog: false,
      AddStaffV: {},
      // 
      AddStaffForm: {
        org: '',
        orgId: '',
        code: '',
        power: '',
        frozen: '',
        name: '',
        duty: '',
        idcard: '',
        numberList: [],
      },
      // 
      EditStaffDialog: false,
      EditStaffV: {},
      // 
      EditStaffForm: {
        org: '',
        orgId: '',
        code: '',
        power: 0,
        frozen: '',
        name: '',
        duty: '',
        idcard: '',
        numberList: [],
      },
      isDie: false,
      // 
      dialogImportFile: false,
      // 
      ImportFileData: {},
      fileList: [],
      // 
      currentPage: 1, // 
      pageSize: 5, // 
      totalrecord: 0, // 
      // 
      changeBindDialog: false,
      changeBindDialogFrom: {
        code: '',
        subrelas: [], // 
      },
      // 
      multipleSelection: [],
      // 
      isChangeDeptTreeDialog: false,
    }
  },
  methods: {
    // todolist
    AddRow(list) {
      this.$axios.get('channel_channelList.action').then(response => { // select
        let dataArr = JSON.parse(response.data)
        // dataArr.unshift({ "value": "call", "label": "" })
        list.push({
          channel: dataArr,
          channelVal: 11,
          master: '',
          trumpet: '',
          upline: true,
          isDisabled: false,
        })
        // debugger;
      }, response => {})
    },
    // 
    delRow(list, row) {
      list.splice(list.indexOf(row), 1);
    },
    // 
    getChannel() {
      this.$axios.get('channel_channelList.action').then(response => {
        this.ChannelGet = JSON.parse(response.data)
      })
    },
    // 
    getDuty() {
      this.$axios.get('staff_getDutyList.action').then(response => {
        this.dutyGet = JSON.parse(response.data)
      })
    },
    // *******  *********
    // ---------------start
    openTreeDialog(param) { // 
      this.isTreeDialog = true;
      this.isSearchAddEditTree = param;
      this.$refs.postinfo.TreeQuery(); // 
    },
    closeDialog() { // 
      this.isTreeDialog = false;
      this.isChangeDeptTreeDialog = false;    // 
    },
    getNodes(nodes) {
      this.isTreeDialog = false;
      if (this.isSearchAddEditTree === 'search') {          // 
        this.staffForm.org = nodes[0].name;
        this.staffForm.orgId = nodes[0].value;
      }else if (this.isSearchAddEditTree === 'add') {       // 
        this.AddStaffForm.org = nodes[0].name;
        this.AddStaffForm.orgId = nodes[0].value;
      }else if (this.isSearchAddEditTree === 'edit') {       // 
        this.EditStaffForm.org = nodes[0].name;
        this.EditStaffForm.orgId = nodes[0].value;
      }
    },
    // 
    // closeTreeInfo(){
    //   // 
    //   this.staffForm.org = "";
    //   this.staffForm.orgId = "";
    //   // 
    //   this.AddStaffForm.org = "";
    //   this.AddStaffForm.orgId = "";
    //   // 
    //   this.EditStaffForm.org = "";
    //   this.EditStaffForm.orgId = "";
    // },
    // ---------------end
    // 
    // 
    queryStaff() {
      this.isDie = false;
      this.staffForm.currentPage = this.currentPage;
      this.getStaffTable()
    },
    // 
    CurrentPageChange(val) {
      this.staffForm.currentPage = val;
      console.log(this.isDie)
      if (this.isDie == "branch") {
        console.log("", val)
        bus.$emit("on-branch-page-change", val)
      } else if (this.isDie == "number") {
        console.log("", val)
        bus.$emit("on-number-page-change", val)
      } else {
        this.getStaffTable()
      }
    },
    // 
    searchTable(data, isDie) {
      console.log("", data, "--------", isDie)
      this.isDie = isDie;
      let staffPage = JSON.parse(data)
      this.pageSize = +staffPage.page_size; // 
      this.totalrecord = +staffPage.totalrecord; // 
      let staffData = staffPage.records;
      this.tableData = staffData; // 
    },
    // 
    getStaffTable() {
      console.log(bus)
      this.tableLoading = true;
      this.$axios.post('staff_queryList.action','jsonData=' + JSON.stringify(this.staffForm)).then(response => {
        this.tableLoading = false;
        console.log(response.data)
        try {
          let staffPage = JSON.parse(response.data)
          this.pageSize = +staffPage.page_size; // 
          this.totalrecord = +staffPage.totalrecord; // 
          let staffData = staffPage.records;
          this.tableData = staffData; // 
        } catch (e) {
          this.$message({ message: "", type: 'info' });
        }
      }, response => {
        this.tableLoading = false;
        this.$message({ message: "" + response, type: 'error' });
      })
    },
    // ===== 
    // 
    AddStaff() {
      console.log("---------", JSON.stringify(this.AddStaffForm))
      this.$axios.post('staff_add.action','jsonData=' + JSON.stringify(this.AddStaffForm)).then(response => {
        this.AddStaffDialog = false;
        try {
          this.$message(JSON.parse(response.data))
        } catch (e) {
          this.$message({ message: "", type: 'info' });
        }
        this.queryStaff();
      }, response => {
        this.AddStaffDialog = false;
        this.$message({ message: "" + response, type: 'error' });
      })
    },
    // 
    export2file() {
      this.exportLoading = true;
      this.$axios.post('staff_exportExcel.action','jsonData=' + JSON.stringify(this.staffForm)).then(response => {
        console.log(response.data)
        var res = JSON.parse(response.data)
        if (res.result == 'success') {
          // window.open(res.filepath);
          this.fileUrl = res.filepath;
          this.export2fileDialog = true;
        } else {
          this.$message({ message: res.cause, type: 'error' });
        }
          this.exportLoading = false;
      }, response => {
        this.exportLoading = false;
        this.$message({ message: "" + response, type: 'error' });
      })
    },
    // 
      ImportFileOk(){
        console.log("ImportFile")
        this.$refs.ImportFile.submit();
        this.dialogImportFile = false;
        console.log(this.ImportFileData, this.fileList)
      },
    // 
    flieSuccess(response, file, fileList) {
      console.log("flieSuccess:", response)
      var res = JSON.parse(response)
      if (res.result == 'success') {
        this.$message({ message: "", type: 'success' });
        this.queryStaff();
      } else {
        this.$message({ message: res.cause, type: 'error' });
      }
      this.fileList = [];
    },
    // 
    fileError(err, file, fileList) {
      this.$message({ message: "", type: 'error' });
      this.fileList = [];
    },
    // ===== 
    // ()
    openEditStaff(i, row) {
      this.EditStaffDialog = true;
      this.EditStaffForm.org = row.org_name;
      this.EditStaffForm.org_id = row.org_id;
      this.EditStaffForm.code = row.code;
      this.EditStaffForm.power = row.data_permission;
      this.EditStaffForm.frozen = row.status;
      this.EditStaffForm.name = row.name;
      this.EditStaffForm.duty = row.duty;
      this.EditStaffForm.idcard = row.idcard;
      // &&
      this.EditStaffForm.numberList = [];
      if (this.EditStaffForm.numberList.length === 0) {
        this.$axios.get('channel_channelList.action').then(response => {
          let dataArr = JSON.parse(response.data)
          // dataArr.unshift({ "value": "call", "label": "" })
          let subrelas = row.subrelas.map(d => {
            this.EditStaffForm.numberList.push({
              channel: dataArr,
              channelVal: d.channelId,
              master: d.anum,
              trumpet: d.xnum,
              upline: d.upline,
              isDisabled: true,
            })
          })
        }, response => {})
      }
    },
    // 
    enterEditStaff() {
      console.log("---------", JSON.stringify(this.EditStaffForm))
      this.$axios.post('staff_update.action','jsonData=' + JSON.stringify(this.EditStaffForm)).then(response => {
        this.EditStaffDialog = false;
        try {
          this.$message(JSON.parse(response.data))
        } catch (e) {
          this.$message({ message: "", type: 'error' });
        }
        this.queryStaff();
      }, response => {
        this.EditStaffDialog = false;
        this.$message({ message: "" + response, type: 'error' });
      })
    },
    // 
    unBind(i, row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.get('staff_axUnSub.action?code=' + row.code).then(response => {
          this.$message({ message: "", type: 'success' });
          this.queryStaff();
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // ==== 
    // 
    openChangeBind(i, row) {
      this.changeBindDialog = true;
      this.changeBindDialogFrom.code = row.code;
      row.subrelas.map(r => {
        // delete r.upline
        // this.$delete(r, "upline")
        this.$set(r, "isChange", true)
      })
      this.changeBindDialogFrom.subrelas = row.subrelas;
    },
    // 
    enterChangeBind() {
      this.changeBindDialog = false;
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.post('staff_changeBinding.action','jsonData=' + JSON.stringify(this.changeBindDialogFrom)).then(response => {
          let resInfo = JSON.parse(response.data)
          if (typeof resInfo.result == 'string') {
            this.$message({ message: "," + resInfo.result, type: 'error', });
            return;
          } else {
            this.$message({ message: "", type: 'success' });
          }
          this.queryStaff();
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // 
    handleDel(i, row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.get('staff_delete.action?code=' + row.code).then(response => {
          this.$message({ message: "", type: 'success', });
          this.queryStaff();
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // 
    resetPwd(i, row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        this.$axios.get("staff_resetPassword.action?code=" + row.code).then(response => {
          this.$alert('' + response.data, '', {
            confirmButtonText: '',
            callback: action => {}
          });
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })

      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // 
    tableSelectionChange(val) {
      this.multipleSelection = val.map((r) => { return r.userId });
      console.log(this.multipleSelection)
    },
    // 
    freeze() {
      console.log(this.multipleSelection.length)
      if (this.multipleSelection.length <= 0) {
        this.$message({ type: 'info', message: '' });
        return;
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.get("staff_freeze.action?userId=" + JSON.stringify(this.multipleSelection)).then(response => {
          this.$message({ type: 'success', message: '' });
          this.queryStaff();
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // 
    unfreeze() {
      if (this.multipleSelection.length <= 0) {
        this.$message({ type: 'info', message: '' });
        return;
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.get("staff_unfreeze.action?userId=" + JSON.stringify(this.multipleSelection)).then(response => {
          this.$message({ type: 'success', message: '' });
          this.queryStaff();
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // 
    upLine() {
      if (this.multipleSelection.length <= 0) {
        this.$message({ type: 'info', message: '' });
        return;
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.get("staff_online.action?userId=" + JSON.stringify(this.multipleSelection)).then(response => {
          this.$message({ type: 'success', message: '' });
          this.queryStaff();
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // 
    downLine() {
      if (this.multipleSelection.length <= 0) {
        this.$message({ type: 'info', message: '' });
        return;
      }
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.get("staff_offline.action?userId=" + JSON.stringify(this.multipleSelection)).then(response => {
          console.log(response.data)
          this.$message({ type: 'success', message: '' });
          this.queryStaff();
        }, response => {
          this.$message({ message: "" + response, type: 'error', });
        })
      }).catch(() => {
        this.$message({ type: 'info', message: '' });
      });
    },
    // 
    unAllBind() {
      if(this.multipleSelection.length <= 0 ){
        this.$message({ type: 'info', message: '' });
      }else{
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.get('staff_batchAxUnSub.action?code=' + this.multipleSelection).then(response => {
            this.$message({ message: "", type: 'success', });
            this.queryStaff();
          }, response => {
            this.$message({ message: "" + response, type: 'error', });
          })
        }).catch(() => {
          this.$message({ type: 'info', message: '' });
        });
      }
    },
    // 
    AllDel() {
      if(this.multipleSelection.length <= 0 ){
        this.$message({ type: 'info', message: '' });
      }else{
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.get('staff_batchDelete.action?code=' + this.multipleSelection).then(response => {
            this.$message({ message: "", type: 'success', });
            this.queryStaff();
          }, response => {
            this.$message({ message: "" + response, type: 'error', });
          })
        }).catch(() => {
          this.$message({ type: 'info', message: '' });
        });
      }
    },
    // 
    changeDept() {
      if(this.multipleSelection.length <= 0 ){
        this.$message({ type: 'info', message: '' });
      }else{
        this.isChangeDeptTreeDialog = true;
        this.$refs.postChangeDept.TreeQuery(); // 
      }
    },
    getChangeDeptNodes(nodes) {
      this.isChangeDeptTreeDialog = false;
      let ChangeDeptInfo = { list: this.multipleSelection, id: nodes[0].value }

      console.log("", JSON.stringify(ChangeDeptInfo))

      this.$axios.post("staff_changeOrg.action","jsonData=" + JSON.stringify(ChangeDeptInfo)).then(response => {
        this.$message({ type: 'success', message: '' });
        this.queryStaff();
      }, response => {
        this.$message({ message: "" + response, type: 'error' });
      })
    },
  },
  created() {
    this.getChannel();
    this.getDuty();
  }
}

</script>
<style lang="less" scoped>
.el-dropdown-menu__item>p {
  margin: 0px;
}

.el-icon-close:hover {
  color: red;
  cursor: pointer;
}
.active-wrapper{
  background-color: #F7BA2A;
}
</style>



