<!--  -->
<template>
  <div>
    <el-form ref="custForm">
      <el-form-item label=":">
        <a href="JavaScript:void(0)" @click="categoryChoice"></a>
      </el-form-item>
      <el-form-item label=":">
        <el-select v-model.trim="custForm.ascription" placeholder="">
          <el-option label="" value="user"></el-option>
          <el-option label="" value="clientPool" :disabled="true"></el-option>
        </el-select>
        <el-select style="width:300px" v-if="custForm.ascription === 'user'" v-model.trim="custForm.user" placeholder="">
          <el-option v-for="item in nameGet" :label="item.userName" :value="item.userId" :key="item.userId"></el-option>
        </el-select>
        <el-input style="width:300px" v-if="custForm.ascription === 'clientPool'" v-model.trim="custForm.clientPool" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-select v-model.trim="custForm.source" placeholder="">
          <el-option v-for="item in sourceGet" :label="item.sourceName" :value="item.sourceId" :key="item.sourceId"></el-option>
        </el-select>
        <el-input style="width:300px" v-model.trim="custForm.fuzzySearch" placeholder="//()"></el-input>
        <el-input style="width:300px" v-model.trim="custForm.tel" placeholder=""></el-input>
      </el-form-item>
    </el-form>
    <el-form ref="Timeform" :inline="true">
      <!--   start-->
      <el-form-item label="" tag="div">
        <el-date-picker v-model.trim="dateValue" type="datetimerange" @change="dateChange" placeholder="">
        </el-date-picker>
      </el-form-item>
      <!--   end-->
      <el-form-item>
        <el-select v-model.trim="custForm.state" placeholder="">
          <el-option v-for="item in stateGet" :label="item.statusName" :value="item.statusId" :key="item.statusId"></el-option>
        </el-select>
        <el-button type="primary" @click="QueryCust"></el-button>
        <el-button type="primary" @click="addCustomer"></el-button>
        <el-button type="primary"></el-button>
        <el-dropdown>
          <el-button type="primary">
            <i class="el-icon-caret-bottom el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-form-item>
    </el-form>
    <!--   start-->
    <el-dialog title="" :visible.sync="categoryDialog" size="tiny">
      <el-form ref="form" :inline="true">
        <el-form-item label=":">
          <el-radio-group v-model.trim="custForm.Customer">
            <el-radio-button v-for="item in customerGet" :label="item.typeId" name="Customer" :vuale="item.typeId" :key="item.typeId">{{ item.typeName }}</el-radio-button> 
          </el-radio-group>
        </el-form-item>
        <el-form-item label=":">
          <el-radio-group v-model.trim="custForm.Product">
            <el-radio-button v-for="item in CProductGet" :label="item.typeId" name="Product" :vuale="item.typeId" :key="item.typeId">{{ item.typeName }}</el-radio-button> 
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="categoryDialog = false"> </el-button>
        <el-button type="primary" @click="categoryEnter"> </el-button>
      </span>
    </el-dialog>
    <!--   end-->
    <!--   start-->
    <el-dialog title="" :visible.sync="addCustomerDialog">
      <el-form ref="form" :inline="true">
        <el-form-item label=":">
          <el-input style="width:200px" v-model.trim="addCustForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model.trim="addCustForm.state" placeholder="">
            <el-option v-for="item in stateGet" :label="item.statusName" :value="item.statusId" :key="item.statusId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-input style="width:200px" v-model.trim="addCustForm.code" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model.trim="addCustForm.source" placeholder="">
            <el-option v-for="item in sourceGet" :label="item.sourceName" :value="item.sourceId" :key="item.sourceId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model.trim="addCustForm.ascription" placeholder="">
            <el-option label="" value="user"></el-option>
            <el-option label="" value="clientPool" :disabled="true"></el-option>
          </el-select>
          <el-select style="width:300px" v-if="addCustForm.ascription === 'user'" v-model.trim="addCustForm.user" placeholder="">
            <el-option v-for="item in nameGet" :label="item.userName" :value="item.userId" :key="item.userId"></el-option>
          </el-select>
          <el-input style="width:300px" v-if="addCustForm.ascription === 'clientPool'" v-model.trim="addCustForm.clientPool" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-radio class="radio" v-model.trim="addCustForm.gender" label="M"></el-radio>
          <el-radio class="radio" v-model.trim="addCustForm.gender" label="F"></el-radio>
        </el-form-item>
        <br />
        <el-form-item label=":">
          <el-radio-group v-model.trim="addCustForm.Customer">
            <el-radio-button v-for="item in customerGet" :label="item.typeId" name="Customer" :vuale="item.typeId" :key="item.typeId">{{ item.typeName }}</el-radio-button> 
          </el-radio-group>
        </el-form-item>
        <el-form-item label=":">
          <el-radio-group v-model.trim="addCustForm.Product">
            <el-radio-button v-for="item in CProductGet" :label="item.typeId" name="Product" :vuale="item.typeId" :key="item.typeId">{{ item.typeName }}</el-radio-button> 
          </el-radio-group>
        </el-form-item>
        <br />
        <el-form-item label=":">
          <el-button auto-complete="off" icon="plus" @click="addNumber"></el-button>
        </el-form-item>
        <!--   start -->
        <el-form-item :label="' '+(index+1)" v-for="(item, index) in addCustForm.telList" :key="index">
          <el-input style="width:200px" v-model.trim="item.tel" placeholder=""></el-input>
          <el-input style="width:200px" v-model.trim="item.FixedLine" placeholder=""></el-input>
        </el-form-item>
        <!--   end -->
        <el-form-item label="">
          <el-input type="textarea" :rows="2" style="width:600px;" placeholder="" v-model.trim="addCustForm.comments"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCustomerDialog = false"> </el-button>
        <el-button type="primary" @click="addCustomerEnter"> </el-button>
      </span>
    </el-dialog>
    <!--   end-->
    <!--   start-->
    <el-dialog title="" :visible.sync="editCustomerDialog">
      <el-form ref="form" :inline="true">
        <el-form-item label=":">
          <el-input style="width:200px" v-model.trim="editCustForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model.trim="editCustForm.state" placeholder="">
            <el-option v-for="item in stateGet" :label="item.statusName" :value="+item.statusId" :key="+item.statusId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-input style="width:200px" v-model.trim="editCustForm.code" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model.trim="editCustForm.source" placeholder="">
            <el-option v-for="item in sourceGet" :label="item.sourceName" :value="item.sourceId" :key="item.sourceId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":">
          <el-select v-model.trim="editCustForm.ascription" placeholder="">
            <el-option label="" value="user"></el-option>
            <el-option label="" value="clientPool" :disabled="true"></el-option>
          </el-select>
          <el-select style="width:300px" v-if="editCustForm.ascription === 'user'" v-model.trim="editCustForm.user" placeholder="">
            <el-option v-for="item in nameGet" :label="item.userName" :value="item.userId" :key="item.userId"></el-option>
          </el-select>
          <el-input style="width:300px" v-if="editCustForm.ascription === 'clientPool'" v-model.trim="editCustForm.clientPool" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-radio class="radio" v-model.trim="editCustForm.gender" label="M"></el-radio>
          <el-radio class="radio" v-model.trim="editCustForm.gender" label="F"></el-radio>
        </el-form-item>
        <br />
        <el-form-item label=":">
          <el-radio-group v-model.trim="editCustForm.Customer">
            <el-radio-button v-for="item in customerGet" :label="item.typeId" name="Customer" :vuale="item.typeId" :key="item.typeId">{{ item.typeName }}</el-radio-button> 
          </el-radio-group>
        </el-form-item>
        <el-form-item label=":">
          <el-radio-group v-model.trim="editCustForm.Product">
            <el-radio-button v-for="item in CProductGet" :label="item.typeId" name="Product" :vuale="item.typeId" :key="item.typeId">{{ item.typeName }}</el-radio-button> 
          </el-radio-group>
        </el-form-item>
        <br />
        <!--   start -->
        <el-form-item :label="' '+(index+1)" v-for="(item, index) in editCustForm.telList" :key="index">
          <el-input style="width:200px" v-model.trim="item.tel" placeholder=""></el-input>
          <el-input style="width:200px" v-model.trim="item.FixedLine" placeholder=""></el-input>
        </el-form-item>
        <!--   end -->
        <el-form-item label="">
          <el-input type="textarea" :rows="2" style="width:600px;" placeholder="" v-model.trim="editCustForm.comments"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCustomerDialog = false"> </el-button>
        <el-button type="primary" @click="editCustomerEnter"> </el-button>
      </span>
    </el-dialog>
    <!--   end-->
    <!--   start-->
    <el-table ref="multipleTable" v-loading="tableLoading" :data="tableData" border tooltip-effect="dark" style="width: 100%">
      <el-table-column type="selection" prop="custId" width="55">
      </el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column label="" width="80px">
        <template scope="scope">
          <span>{{ scope.row.gender ==='F' ?"":"" }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="sourceName" label="">
      </el-table-column>
      <el-table-column prop="comments" :show-overflow-tooltip="true" label="">
      </el-table-column>
      <el-table-column label="" width="80px">
        <template scope="scope">
          <span>{{ scope.row.belongWith ==='C' ?"":"" }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="statusName" label="">
      </el-table-column>
      <el-table-column prop="situation" label="">
      </el-table-column>
      <el-table-column :show-overflow-tooltip="true" label="">
        <template scope="scope">
          <span v-if="scope.row.sourceId !== 3" v-for="item in scope.row.telList">
            {{item.tel}} , {{item.FixedLine}}
          </span>
          <span v-if="scope.row.sourceId === 3">{{scope.row.telList}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="" label="">
        <template scope="scope">
          <el-button @click.native.prevent="editCustomer(scope.$index, scope.row)" type="text" size="small"></el-button>
          <el-dropdown trigger="click">
            <span class="el-dropdown-link">
              <i class="el-icon-caret-bottom el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item></el-dropdown-item>
              <el-dropdown-item @click.native.prevent="delCustomer(scope.$index, scope.row)"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <br />
    <!--   end-->
    <!--   start-->
    <el-row>
      <el-col :offset="8">
        <el-pagination @current-change="CurrentPageChange" :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper,total" :total="totalrecord">
        </el-pagination>
      </el-col>
    </el-row>
    <br />
    <!--   end-->
  </div>
</template>
<script>
import treeMenu from '@/components/selectSector'

export default {
  data() {
    return {
      // 
      nameGet: [],
      // 
      customerGet: [],
      // 
      CProductGet: [],
      // 
      sourceGet: [],
      // 
      stateGet: [],
      // 
      categoryDialog: false,
      // 
      custForm: {
        ascription: 'user', // 
        user: '', // 
        clientPool: '', // 
        source: '', // 
        Customer: '',
        Product: '',
        fuzzySearch: '',
        tel: '',
        state: '',
        // 
        dateVal: '',
      },
      // 
      addCustForm: {
        ascription: 'user', // 
        user: '', // 
        clientPool: '', // 
        code: '', // 
        source: '', // 
        gender: 'M',
        Customer: '',
        Product: '',
        telList: [{
          tel: '',
          FixedLine: '',
        }],
        state: '',
      },
      // 
      addCustomerDialog: false,
      // 
      editCustForm: {
        ascription: 'user', // 
        user: '', // 
        clientPool: '', // 
        code: '', // 
        source: '', // 
        gender: 'M',
        Customer: '',
        Product: '',
        telList: [{
          tel: '',
          FixedLine: '',
        }],
        state: '',
      },
      // 
      editCustomerDialog: false,
      // 
      dateValue: '',
      // 
      audioSrc: '',
      // ()
      tableData: [],
      tableLoading: false,
      // --1
      currentPage: 1, // 
      pageSize: 5, // 
      totalrecord: 0, // 
    };
  },
  methods: {
    //  && 
    getCP() {
      // 
      this.$axios.get('customer_queryCustomerType.action').then(response => {
        this.customerGet = JSON.parse(response.data)
      })
      // 
      this.$axios.get('customer_queryCustomerProductType.action').then(response => {
        this.CProductGet = JSON.parse(response.data)
      })
    },
    queryUsername() { // 
      return this.$axios.get('customer_queryUsername.action');
    },
    queryCustomerSource() { // 
      return this.$axios.get('customer_queryCustomerSource.action');
    },
    queryCustomerStatus() { // 
      return this.$axios.get('customer_queryCustomerStatus.action');
    },
    //   &&  && 
    getSSN() {
      // // 
      // this.$axios.get('customer_queryUsername.action').then(response => {
      //   this.nameGet = JSON.parse(response.data)
      // })
      // // 
      // this.$axios.get('customer_queryCustomerSource.action').then(response => {
      //   this.sourceGet = JSON.parse(response.data)
      // })
      // // 
      // this.$axios.get('customer_queryCustomerStatus.action').then(response => {
      //   this.stateGet = JSON.parse(response.data)
      // })

      //  
      this.$axios.all([this.queryUsername(), this.queryCustomerSource(), this.queryCustomerStatus()])
        .then(this.$axios.spread((queryUsername, queryCustomerSource, queryCustomerStatus) => {
          console.log(queryUsername.data)
          this.nameGet = JSON.parse(queryUsername.data)
          this.sourceGet = JSON.parse(queryCustomerSource.data)
          this.stateGet = JSON.parse(queryCustomerStatus.data)
        }, response => {
          this.$message({ message: "  &&  &&  " + response, type: 'error', });
        }));
    },
    // ======== 
    // 
    categoryChoice() {
      this.categoryDialog = true;
      this.getCP();
    },
    // 
    categoryEnter() {
      this.categoryDialog = false
    },
    // ======== 
    // 
    addCustomer() {
      this.addCustomerDialog = true;
      this.getCP();
      this.getSSN();
    },
    // - 
    addNumber() {
      this.addCustForm.telList.push({ tel: '', FixedLine: '', })
    },
    // 
    addCustomerEnter() {
      console.log(JSON.stringify(this.addCustForm))
      this.$axios.post('customer_CustomerAdd.action','jsonData=' + JSON.stringify(this.addCustForm)).then(response => {
        this.addCustomerDialog = false;
        if (response.data === 'success') {
          this.QueryCust();
          this.$message({ message: "", type: 'success', });
        } else if (response.data === 'false') {
          this.$message({ message: "", type: 'error', });
        } else {
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.addCustomerDialog = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // ======== 
    // 
    editCustomer(i, row) {
      this.editCustomerDialog = true;
      this.getCP();
      this.getSSN();

      this.editCustForm.custId = row.custId
      this.editCustForm.name = row.name
      this.editCustForm.state = +row.statusId
      this.editCustForm.code = row.code
      this.editCustForm.source = row.sourceId
      this.editCustForm.ascription = row.belongWith === 'P' ? 'clientPool' : 'user'
      this.editCustForm.user = row.userId
      this.editCustForm.clientPool = row.clientPool
      this.editCustForm.gender = row.gender
      this.editCustForm.Customer = row.clevelId
      this.editCustForm.Product = row.plevelId
      this.editCustForm.comments = row.comments
      this.editCustForm.telList = row.telList
    },
    // - 
    editNumber() {
      this.editCustForm.telList.push({ tel: '', FixedLine: '', })
    },
    // 
    editCustomerEnter() {
      console.log(JSON.stringify(this.editCustForm))
      this.$axios.post('customer_CustomerUpdate.action','jsonData=' + JSON.stringify(this.editCustForm)).then(response => {
        this.editCustomerDialog = false;
        console.log(response.data)
        if (response.data === 'success') {
          this.QueryCust();
          this.$message({ message: "", type: 'success', });
        } else if (response.data === 'false') {
          this.$message({ message: "", type: 'error', });
        } else {
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.editCustomerDialog = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // ========  
    delCustomer(i, row) {
      this.$axios.get('customer_CustomerDelete.action?custId=' + row.custId).then(response => {
        console.log(response.data)
        this.QueryCust();
        this.$message({ message: "", type: 'success', });
      }, response => {
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // ======== 
    // 
    QueryCust() {
      this.custForm.currentPage = this.currentPage;
      this.getCustTable()
    },
    // 
    CurrentPageChange(val) {
      this.custForm.currentPage = val;
      this.getCustTable()
    },
    // 
    getCustTable() {
      this.tableLoading = true;
      this.$axios.post('customer_queryCustomerList.action','jsonData=' + JSON.stringify(this.custForm)).then(response => {
        this.tableLoading = false;
        try {
          let custFmt = JSON.parse(response.data)
          let custData = custFmt.pageview
          this.tableData = custData.records; // 
          this.pageSize = +custData.page_size; // 
          this.totalrecord = +custData.totalrecord; // 
          console.log(response.data)
        } catch (e) {
          this.tableData = [];
          this.$message({ message: "", type: 'error', });
        }
      }, response => {
        this.tableLoading = false;
        this.$message({ message: "" + response, type: 'error', });
      })
    },
    // 
    dateChange(val) {
      this.custForm.dateVal = val;
    },
  },
  created() {
    this.getSSN();
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



